// @ts-ignore
import avatarImg from './__screenshots__/mentions-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'AMentions',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'AMentions'
    },
    props: [
        {
            name: 'defaultValue',
            title: { label: '', tip: 'defaultValue|' },
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            name: 'value',
            title: { label: '', tip: 'value|' },
            propType: 'string',
        },
        {
            name: 'autoFocus',
            title: { label: '', tip: 'autoFocus|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'filterOption',
            title: { label: '', tip: 'filterOption|' },
            propType: { type: 'oneOfType', value: ['bool', 'func'] },
        },
        {
            name: 'notFoundContent',
            title: {
                label: '',
                tip: 'notFoundContent|',
            },
            propType: 'node',
        },
        {
            name: 'placement',
            title: { label: '', tip: 'placement|' },
            propType: { type: 'oneOf', value: ['top', 'bottom'] },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        {
                            title: '',
                            value: 'top',
                        },
                        {
                            title: '',
                            value: 'bottom',
                        },
                    ],
                },
            },
        },
        {
            name: 'prefix',
            title: { label: '', tip: 'prefix|' },
            propType: {
                type: 'oneOfType',
                value: ['string', { type: 'arrayOf', value: 'string' }],
            },
        },
        {
            name: 'split',
            title: { label: '', tip: 'split|' },
            propType: 'string',
        },
        {
            name: 'validateSearch',
            title: { label: '', tip: 'validateSearch|' },
            propType: 'func',
        },
        // {
        //   name: 'onChange ',
        //   title: { label: '', tip: 'onChange|' },
        //   propType: 'func',
        // },
        // {
        //   name: 'onSelect',
        //   title: { label: '', tip: 'onSelect|' },
        //   propType: 'func',
        // },
        // {
        //   name: 'onSearch',
        //   title: { label: '', tip: '' },
        //   propType: 'func',
        // },
        // {
        //   name: 'onFocus',
        //   title: { label: '', tip: '' },
        //   propType: 'func',
        // },
        // {
        //   name: 'onBlur',
        //   title: { label: '', tip: '' },
        //   propType: 'func',
        // },
        {
            name: 'getPopupContainer',
            title: {
                label: ' HTML ',
                tip: 'getPopupContainer| HTML ',
            },
            propType: 'func',
        }
    ],
    configure: {
        supports: {
            style: true
        },
        events: [
            {
                name: 'onChange ',
                template: "onChange (text,${extParams}){\n// \nconsole.log('onChange ',text);}",
            },
            {
                name: 'onSelect',
                template: "onSelect(option,prefix,${extParams}){\n// \nconsole.log('onSelect',option,prefix);}",
            },
            {
                name: 'onSearch',
                template: "onSearch(text,prefix,${extParams}){\n// \nconsole.log('onSearch',text,prefix);}",
            },
            {
                name: 'onFocus',
                template: "onFocus(${extParams}){\n// \nconsole.log('onFocus');}",
            },
            {
                name: 'onBlur',
                template: "onBlur(${extParams}){\n// \nconsole.log('onBlur');}",
            },
        ]
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "AMentions",
                props: {
                    getPopupContainer: () => document.querySelector('#lce-container')
                }
            }
        }
    ]
};
