import { uuid } from '../../utils/index';
import { itemsExtraProps } from './util';
// @ts-ignore
import avatarImg from './__screenshots__/menu-1.jpg?inline';
export default {
    group: 'ant-vue',
    componentName: 'AMenu',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'AMenu'
    },
    props: [
        {
            name: 'items',
            title: { label: '', tip: 'items | ' },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'ObjectSetter',
                        props: {
                            config: {
                                items: [
                                    {
                                        name: 'key',
                                        title: 'key',
                                        setter: 'StringSetter',
                                        initialValue: (val) => val || uuid(),
                                    },
                                    {
                                        name: 'children',
                                        title: { label: '', tip: 'children|' },
                                        setter: 'StringSetter',
                                    },
                                    {
                                        name: 'category',
                                        title: {
                                            label: '',
                                            tip: 'category|',
                                        },
                                        propType: {
                                            type: 'oneOf',
                                            value: ['AMenuItem', 'ASubMenu', 'AMenuItemGroup'],
                                        },
                                        setter: [
                                            {
                                                componentName: 'RadioGroupSetter',
                                                props: {
                                                    options: [
                                                        {
                                                            title: 'Item',
                                                            value: 'AMenuItem',
                                                        },
                                                        {
                                                            title: 'SubMenu',
                                                            value: 'ASubMenu',
                                                        },
                                                        {
                                                            title: 'ItemGroup',
                                                            value: 'AMenuItemGroup',
                                                        },
                                                    ],
                                                },
                                            },
                                            'VariableSetter',
                                        ],
                                    },
                                ],
                            },
                        },
                        initialValue: () => {
                            return {
                                key: 'item-' + uuid(),
                                category: 'AMenuItem',
                                children: '',
                            };
                        },
                    },
                },
            },
            extraProps: itemsExtraProps,
        },
        {
            name: 'defaultOpenKeys',
            title: {
                label: '',
                tip: 'defaultOpenKeys |  SubMenu  key ',
            },
            propType: { type: 'arrayOf', value: 'string' },
        },
        {
            name: 'defaultSelectedKeys',
            title: { label: '', tip: 'defaultSelectedKeys |  key ' },
            propType: { type: 'arrayOf', value: 'string' },
        },
        {
            name: 'forceSubMenuRender',
            title: {
                label: '',
                tip: 'forceSubMenuRender |  DOM',
            },
            propType: 'bool',
            defaultValue: false,
        },
        {
            name: 'inlineCollapsed',
            title: { label: '', tip: 'inlineCollapsed | inline ' },
            propType: 'bool',
        },
        {
            name: 'inlineIndent',
            title: { label: '', tip: 'inlineIndent | inline ' },
            propType: 'number',
        },
        {
            name: 'mode',
            title: {
                label: '',
                tip: 'mode | ',
            },
            propType: { type: 'oneOf', value: ['vertical', 'horizontal', 'inline'] },
        },
        {
            name: 'multiple',
            title: { label: '', tip: 'multiple | ' },
            propType: 'bool',
            defaultValue: false,
        },
        {
            name: 'openKeys',
            title: {
                label: '',
                tip: 'openKeys |  SubMenu  key ',
            },
            propType: { type: 'arrayOf', value: 'string' },
        },
        {
            name: 'selectable',
            title: { label: '', tip: 'selectable | ' },
            propType: 'bool',
            defaultValue: true,
        },
        {
            name: 'selectedKeys',
            title: { label: '', tip: 'selectedKeys |  key ' },
            propType: { type: 'arrayOf', value: 'string' },
        },
        {
            name: 'style',
            title: { label: '', tip: 'style | ' },
            propType: 'object',
        },
        {
            name: 'subMenuCloseDelay',
            title: {
                label: '',
                tip: 'subMenuCloseDelay | ',
            },
            propType: 'number',
        },
        {
            name: 'subMenuOpenDelay',
            title: {
                label: '',
                tip: 'subMenuOpenDelay | ',
            },
            propType: 'number',
        },
        {
            name: 'theme',
            title: { label: '', tip: 'theme | ' },
            propType: { type: 'oneOf', value: ['light', 'dark'] },
        },
        {
            name: 'onClick',
            title: { label: ' MenuItem ', tip: 'onClick |  MenuItem ' },
            propType: 'func',
        },
        {
            name: 'onDeselect',
            title: {
                label: '',
                tip: 'onDeselect |  multiple ',
            },
            propType: 'func',
        },
        {
            name: 'triggerSubMenuAction',
            title: {
                label: '',
                tip: 'triggerSubMenuAction | /',
            },
            propType: { type: 'oneOf', value: ['hover', 'click'] },
        },
        {
            name: 'onOpenChange',
            title: { label: 'SubMenu /', tip: 'onOpenChange | /' },
            propType: 'func',
        },
        {
            name: 'onSelect',
            title: { label: '', tip: 'onSelect | ' },
            propType: 'func',
        },
        {
            name: 'overflowedIndicator',
            title: { label: '', tip: 'overflowedIndicator |  Menu ' },
            propType: 'node',
        },
    ],
    configure: {
        supports: {
            style: true,
            events: [
                {
                    name: 'onClick',
                    template: "onClick({item,key,keyPath,domEvent},${extParams}){\n//  MenuItem \nconsole.log('onClick',item,key,keyPath,domEvent);}",
                },
                {
                    name: 'onDeselect',
                    template: "onDeselect({item,key,keyPath,selectedKeys,domEvent},${extParams}){\n//  multiple \nconsole.log('onDeselect',item,key,keyPath,selectedKeys,domEvent);}",
                },
                {
                    name: 'onOpenChange',
                    template: "onOpenChange(openKeys,${extParams}){\n// SubMenu /\nconsole.log('onOpenChange',openKeys);}",
                },
                {
                    name: 'onSelect',
                    template: "onSelect({item,key,keyPath,selectedKeys,domEvent},${extParams}){\n// \nconsole.log('onSelect',item,key,keyPath,selectedKeys,domEvent);}",
                },
            ],
        }
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "AMenu",
                props: {
                    style: {
                        height: '80px',
                        lineHeight: '80px'
                    },
                    mode: "inline",
                    defaultSelectedKeys: ["1"],
                    defaultOpenKeys: ["sub1"],
                    theme: "dark",
                    items: [
                        {
                            key: "item-i5wd",
                            category: "AMenuItem",
                            children: ""
                        }
                    ]
                }
            }
        }
    ]
};
