export default {
    group: 'ant-vue',
    componentName: 'ATimelineItem',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ATimelineItem'
    },
    props: [
        {
            name: 'color',
            title: { label: '', tip: "color|" },
            propType: 'string',
            setter: 'StringSetter',
            defaultValue: 'blue'
        },
        {
            name: 'dot',
            title: { label: '', tip: 'dot|' },
            propType: { type: 'oneOfType', value: ['string', 'node'] }
        },
        {
            name: 'label',
            title: { label: '', tip: 'label|' },
            propType: { type: 'oneOfType', value: ['string', 'node'] }
        },
        {
            name: 'position',
            title: { label: '', tip: 'position' },
            propType: {
                type: 'oneOf',
                value: ['left', 'right']
            },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        { title: 'left', value: 'left' },
                        { title: 'right', value: 'right' }
                    ]
                }
            },
            defaultValue: 'left'
        }
    ],
    configure: {
        supports: {
            style: true
        }
    },
    snippets: []
};
