// @ts-ignore
import avatarImage from './__screenshots__/form-1.png?inline';
/* eslint-disable */
import { uuid } from '../../utils/index';
// @ts-ignore
export default {
    group: 'ant-vue',
    componentName: 'AForm',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'AForm'
    },
    props: [
        {
            name: 'ref',
            title: {
                label: 'ref',
                tip: 'ref |  this.$refs[\'xxx\'] ',
            },
            defaultValue: () => {
                return `form_${uuid()}`;
            },
            setter: 'StringSetter',
            supportVariable: true
        },
        {
            name: 'model',
            title: { label: '', tip: 'model | ' },
            propType: 'object',
            setter: 'JsonSetter',
            supportVariable: true
        },
        {
            name: 'rules',
            title: { label: '', tip: 'rules | ' },
            propType: 'object',
            setter: 'JsonSetter',
            supportVariable: true
        },
        {
            name: 'colon',
            title: { label: '', tip: 'colon | ' },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter',
            supportVariable: true
        },
        {
            name: 'hideRequiredMark',
            title: { label: '', tip: 'hideRequiredMark | ' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter',
            supportVariable: true
        },
        {
            type: 'group',
            title: '',
            display: 'accordion',
            items: [
                {
                    name: 'labelCol',
                    title: { label: '', tip: 'labelCol | ' },
                    display: 'inline',
                    setter: {
                        componentName: 'ObjectSetter',
                        props: {
                            config: {
                                items: [
                                    {
                                        name: 'span',
                                        title: '',
                                        setter: {
                                            componentName: 'NumberSetter',
                                            props: {
                                                min: 0,
                                                max: 24,
                                            },
                                        },
                                    },
                                    {
                                        name: 'offset',
                                        title: '',
                                        setter: {
                                            componentName: 'NumberSetter',
                                            props: {
                                                min: 0,
                                                max: 24,
                                            },
                                        },
                                    },
                                ],
                            },
                        },
                    },
                    description: 'label  `<Col>`  span offset  {span: 8, offset: 16}',
                },
                {
                    name: 'wrapperCol',
                    title: { label: '', tip: 'wrapperCol | ' },
                    display: 'inline',
                    setter: {
                        componentName: 'ObjectSetter',
                        props: {
                            config: {
                                items: [
                                    {
                                        name: 'span',
                                        title: '',
                                        setter: {
                                            componentName: 'NumberSetter',
                                            props: {
                                                min: 0,
                                                max: 24,
                                            },
                                        },
                                    },
                                    {
                                        name: 'offset',
                                        title: '',
                                        setter: {
                                            componentName: 'NumberSetter',
                                            props: {
                                                min: 0,
                                                max: 24,
                                            },
                                        },
                                    },
                                ],
                            },
                        },
                    },
                    description: ' labelCol',
                },
            ]
        },
        {
            name: 'labelAlign',
            title: {
                label: '',
                tip: 'labelAlign | label',
            },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        {
                            title: '',
                            value: 'left',
                        },
                        {
                            title: '',
                            value: 'right',
                        },
                    ],
                },
            },
            propType: { type: 'oneOf', value: ['left', 'right'] },
            defaultValue: 'right',
        },
        {
            name: 'layout',
            title: { label: '', tip: 'layout | ' },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        {
                            title: '',
                            value: 'horizontal',
                        },
                        {
                            title: '',
                            value: 'vertical',
                        },
                        {
                            title: '',
                            value: 'inline',
                        },
                    ],
                },
            },
            propType: { type: 'oneOf', value: ['horizontal', 'vertical', 'inline'] },
            defaultValue: 'horizontal',
        },
        {
            name: 'name',
            title: {
                label: '',
                tip: 'name |  `id` ',
            },
            propType: 'string',
            setter: 'StringSetter',
            supportVariable: true
        },
        {
            name: 'validateOnRuleChange',
            title: {
                label: 'rules',
                tip: 'validateOnRuleChange |  rules ',
            },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter',
            supportVariable: true
        },
        {
            name: 'scrollToFirstError',
            title: {
                label: '',
                tip: 'scrollToFirstError | ',
            },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter',
            supportVariable: true
        },
        {
            name: 'size',
            title: {
                label: '',
                tip: 'size |  antd ',
            },
            propType: { type: 'oneOf', value: ['small', 'middle', 'large'] },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        {
                            title: '',
                            value: 'large',
                        },
                        {
                            title: '',
                            value: 'middle',
                        },
                        {
                            title: '',
                            value: 'small',
                        },
                    ],
                },
            },
            defaultValue: 'middle',
        },
        {
            type: 'group',
            title: '',
            display: 'accordion',
            items: [
                {
                    name: 'validateMessages',
                    title: { label: '', tip: 'validateMessages | ' },
                    setter: 'JsonSetter',
                    defaultValue: { required: "'${name}' " },
                },
                {
                    name: 'validateTrigger',
                    title: { label: '', tip: 'validateTrigger | ' },
                    propType: {
                        type: 'oneOf',
                        value: ['onChange', 'onBlur'],
                    },
                    setter: {
                        componentName: 'RadioGroupSetter',
                        props: {
                            options: [
                                {
                                    title: '',
                                    value: 'onChange',
                                },
                                {
                                    title: '',
                                    value: 'onBlur',
                                },
                            ],
                        },
                    },
                },
                {
                    name: 'onClearValidate',
                    title: {
                        label: '',
                        tip: 'onClearValidate |  name  name ',
                    },
                    propType: 'func',
                },
                {
                    name: 'onResetFields',
                    title: {
                        label: '',
                        tip: 'onResetFields | ',
                    },
                    propType: 'func',
                },
                {
                    name: 'onValidateFields',
                    title: { label: '', tip: 'onValidateFields | ' },
                    propType: 'func',
                },
                {
                    name: 'onValidate',
                    title: {
                        label: '',
                        tip: 'onValidate | ',
                    },
                    propType: 'func',
                }
            ]
        }
    ],
    configure: {
        component: {
            isContainer: true
        },
        supports: {
            style: true,
            events: [
                {
                    name: 'onFinish',
                    template: "onFinish(values,${extParams}){\n// \nconsole.log('onFinish',values);}",
                },
                {
                    name: 'onFinishFailed',
                    template: "onFinishFailed({values,errorFields,outOfDate},${extParams}){\n// \nconsole.log('onFinishFailed',values, errorFields, outOfDate);}",
                },
                {
                    name: 'onSubmit',
                    template: "onSubmit(e,${extParams}){\n// \nconsole.log('onSubmit',e);}",
                },
                {
                    name: 'onValidate',
                    template: "onValidate(name,status,errorMsgs,${extParams}){\n// \nconsole.log('onValidate',name,status,errorMsgs);}",
                },
            ]
        },
        advanced: {
            callbacks: {
                onNodeAdd: (dragment, currentNode) => {
                    const comps = [
                        'AInput',
                        'ASelect',
                        'ARadio',
                        'ARadioGroup',
                        'ACheckbox',
                        'ACheckboxGroup',
                        'ASwitch',
                        'AUpload',
                        'ADatePicker',
                        'ARate',
                        'ATransfer',
                    ];
                    if (!dragment ||
                        !dragment.componentMeta ||
                        !dragment.componentMeta.npm ||
                        !dragment.componentMeta.npm.package ||
                        dragment.componentMeta.npm.package.indexOf('lowcode-material-ant-vue') === -1 ||
                        comps.every((comp) => dragment.componentName.indexOf(comp) === -1)) {
                        return;
                    }
                    // P
                    const layoutPNode = currentNode.document.createNode({
                        componentName: 'AFormItem',
                        props: {
                            label: ': ',
                        },
                        children: [dragment.exportSchema()],
                    });
                    // dragmentnode,setTimeout
                    setTimeout(() => {
                        currentNode.replaceChild(dragment, layoutPNode.exportSchema(), 
                        //  nodeId
                        { reserveSchemaNodeId: true });
                    }, 1);
                },
            },
        }
    },
    snippets: [
        {
            title: '',
            componentName: 'AForm',
            props: {
                labelCol: {
                    span: 6,
                    labelAlign: 'right'
                },
                wrapperCol: {
                    span: 14
                },
                onValidate: {
                    type: "JSFunction",
                    value: "function onValidate(e) {\n  console.log('e', changedValues, allValues);\n}"
                },
                onFinish: {
                    type: "JSFunction",
                    value: "function onFinish(values) {\n  console.log('onFinish', values);\n}"
                },
                onFinishFailed: {
                    type: "JSFunction",
                    value: "function onFinishFailed({ values, errorFields, outOfDate }) {\n  console.log('onFinishFailed', values, errorFields, outOfDate);\n}"
                },
                onSubmit: {
                    type: "JSFunction",
                    value: "function onSubmit(e){\n console.log('onSubmit',e);\n}"
                },
                name: "basic"
            },
            children: []
        },
        {
            title: '',
            screenshot: avatarImage,
            schema: {
                componentName: 'AForm',
                props: {
                    style: {
                        backgroundColor: '#fff',
                        padding: '10px',
                        display: 'flex',
                        justifyContent: 'flex-start',
                        alignItems: 'center'
                    },
                    labelCol: {
                        span: 6,
                        labelAlign: 'right'
                    },
                    wrapperCol: {
                        span: 14
                    },
                    onValidate: {
                        type: "JSFunction",
                        value: "function onValidate(e) {\n  console.log('e', changedValues, allValues);\n}"
                    },
                    onFinish: {
                        type: "JSFunction",
                        value: "function onFinish(values) {\n  console.log('onFinish', values);\n}"
                    },
                    onFinishFailed: {
                        type: "JSFunction",
                        value: "function onFinishFailed({ values, errorFields, outOfDate }) {\n  console.log('onFinishFailed', values, errorFields, outOfDate);\n}"
                    },
                    onSubmit: {
                        type: "JSFunction",
                        value: "function onSubmit(e){\n console.log('onSubmit',e);\n}"
                    },
                    name: "basic"
                },
                children: [
                    {
                        componentName: 'AFormItem',
                        props: {
                            label: '',
                            labelAlign: "right",
                            colon: true,
                            required: true,
                            noStyle: false,
                            valuePropName: "value",
                            name: 'b',
                            requiredobj: {
                                required: false,
                                message: ""
                            },
                            typeobj: {
                                type: null,
                                message: null
                            },
                            lenobj: {
                                max: null,
                                min: null,
                                message: null
                            },
                            patternobj: {
                                pattern: null,
                                message: null
                            }
                        },
                        children: []
                    }
                ]
            },
        }
    ],
};
