// @ts-ignore
export default {
    group: 'ant-vue',
    componentName: 'ASelectOption',
    title: '-',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ASelectOption'
    },
    props: [
        {
            title: { label: '', tip: 'class|' },
            name: 'class',
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            title: { label: '', tip: 'disabled|' },
            name: "disabled",
            propType: 'bool',
            setter: 'BoolSetter'
        },
        {
            title: { label: 'key', tip: 'key' },
            name: 'key',
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            title: 'title',
            name: 'title',
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            title: 'value',
            name: 'value',
            propType: {
                type: 'oneOfType', value: ['string', 'number']
            },
            setter: ['StringSetter', 'NumberSetter']
        }
    ],
    configure: {
        component: {
            nestingRule: {
                parentWhitelist: ['ASelect']
            }
        }
    }
};
