/* eslint-disable */
// @ts-ignore
import avatarImg from './__screenshots__/select-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ASelect',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ASelect'
    },
    props: [
        {
            name: 'defaultValue',
            title: { label: '', tip: 'defaultValue | ' },
            propType: {
                type: 'oneOfType',
                value: ['string', { type: 'arrayOf', value: 'string' }, 'number', { type: 'arrayOf', value: 'number' }]
            }
        },
        {
            name: 'v-model:value',
            title: { label: '', tip: 'v-model:value | ' },
            propType: {
                type: 'oneOfType',
                value: ['string', { type: 'arrayOf', value: 'string' }, 'number', { type: 'arrayOf', value: 'number' }]
            }
        },
        {
            name: 'options',
            title: { label: '', tip: 'options | ' },
            propType: {
                type: 'arrayOf',
                value: {
                    type: 'shape',
                    value: [
                        { name: 'label', propType: 'string', description: '', defaultValue: '' },
                        { name: 'value', propType: ['string', 'number'], description: '', defaultValue: '' },
                        { name: 'disabled', propType: 'bool', description: '', defaultValue: false }
                    ]
                }
            },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'ObjectSetter',
                        props: {
                            config: {
                                items: [
                                    { name: 'label', title: '', setter: ['StringSetter', 'VariableSetter'], isRequired: true },
                                    { name: 'value', title: '', setter: ['StringSetter', 'NumberSetter', 'VariableSetter'], isRequired: true },
                                    { name: 'disabled', title: '', setter: ['BoolSetter', 'VariableSetter'] }
                                ]
                            }
                        },
                        initialValue: () => {
                            return {
                                label: '',
                                value: 'test',
                                disabled: false
                            };
                        }
                    }
                }
            }
        },
        {
            title: { label: 'label', tip: 'fieldNames | labelvalueoptions' },
            name: 'fieldNames',
            propType: 'object',
            setter: 'JsonSetter',
            supportVariable: true
        },
        {
            title: { label: '', tip: 'allowClear | ' },
            name: 'allowClear',
            propType: 'bool',
            setter: 'BoolSetter',
            defaultValue: true,
            supportVariable: true
        },
        {
            title: { label: '', tip: 'autoFocus | ' },
            name: 'autoFocus',
            propType: 'bool',
            setter: 'BoolSetter',
            defaultValue: false,
        },
        {
            name: 'labelInValue',
            title: {
                label: 'label',
                tip: 'labelInValue |  label  value ',
            },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            title: { label: '', tip: 'placeholder | ' },
            name: 'placeholder',
            propType: {
                type: 'oneOfType',
                value: ['string', 'node']
            },
            setter: ['StringSetter', 'SlotSetter']
        },
        {
            title: { label: '', tip: 'bordered | ' },
            name: 'bordered',
            propType: 'bool',
            setter: 'BoolSetter',
            defaultValue: true
        },
        {
            title: { label: '', tip: 'filterOption | ' },
            name: 'filterOption',
            propType: {
                type: 'oneOfType',
                value: ['bool', 'func']
            },
            defaultValue: false
        },
        {
            title: { label: '', tip: 'optionFilterProp | ' },
            name: 'optionFilterProp',
            propType: {
                type: 'oneOf',
                value: ['value', 'label']
            },
            defaultValue: 'value'
        },
        {
            title: { label: '', tip: 'getPopupContainer | ' },
            name: 'getPopupContainer',
            propType: { type: 'oneOfType', value: ['node', 'func'] },
        },
        {
            title: { label: 'option', tip: 'optionLabelProp |  Option  Option  value' },
            name: 'optionLabelProp',
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            title: { label: 'autoClearSearchValue', tip: 'autoClearSearchValue |  mode  multiple  tags ' },
            name: 'autoClearSearchValue',
            propType: 'bool',
            setter: 'BoolSetter',
            defaultValue: true
        },
        {
            name: 'showArrow',
            title: { label: '', tip: 'showArrow | ' },
            propType: 'bool',
            setter: 'BoolSetter'
        },
        {
            title: { label: '', tip: 'showSearch | ' },
            name: 'showSearch',
            propType: 'bool',
            setter: 'BoolSetter',
            defaultValue: false,
            supportVariable: true
        },
        {
            title: { label: "", tip: 'size | ' },
            name: 'size',
            propType: {
                type: 'oneOf',
                value: ['default', 'large', 'small']
            },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        { label: 'default', value: 'default' },
                        { label: 'large', value: 'large' },
                        { label: 'small', value: 'small' }
                    ]
                }
            },
            defaultValue: 'default'
        },
        {
            title: { label: '', tip: 'mode | ' },
            name: 'mode',
            propType: { type: 'oneOf', value: ['multiple', 'tags', 'combobox'] },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        { title: '', value: 'multiple' },
                        { title: '', value: 'tags' },
                        { title: 'combobox', value: 'combobox' }
                    ],
                    defaultValue: 'tags',
                }
            },
            supportVariable: true
        },
        {
            name: 'maxTagCount',
            title: { label: 'tag', tip: 'maxTagCount | tag' },
            propType: 'number',
            condition(target) {
                return target.getProps().getPropValue('mode') === 'tags';
            }
        },
        {
            name: 'maxTagTextLength',
            title: { label: 'tag', tip: 'maxTagTextLength | tag' },
            propType: 'number',
            condition(target) {
                return target.getProps().getPropValue('mode') === 'tags';
            }
        },
        {
            name: 'searchValue',
            title: { label: '', tip: 'searchValue | ' },
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            title: { label: '', tip: 'notFoundContent' },
            name: 'notFoundContent',
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            name: 'dropdownRender',
            title: { label: '', tip: 'dropdownRender|' },
            propType: { type: 'oneOfType', value: ['node', 'func'] },
            setter: [
                {
                    componentName: 'SlotSetter',
                    title: '',
                    initialValue: {
                        type: 'JSSlot',
                        params: ['menuNode'],
                        value: [],
                    },
                },
                {
                    componentName: 'FunctionSetter',
                    props: {
                        template: 'dropdownRender(menuNode,props,${extParams}){\n// \nconsole.log("dropdownRender menuNode",menuNode,"props",props)\n}',
                    },
                }
            ]
        },
        {
            name: 'clearIcon',
            title: { label: '', tip: 'clearIcon | ' },
            propType: 'node',
            setter: {
                componentName: 'SlotSetter',
                initialValue: {
                    type: 'JSSlot',
                    value: [
                        {
                            componentName: 'AIcon',
                            props: {
                                type: 'CloseCircleFilled',
                                size: 12,
                                color: '#ddd',
                                rotate: 0,
                                spin: false,
                            }
                        }
                    ]
                }
            }
        },
        {
            name: 'suffixIcon',
            title: { label: '', tip: 'suffixIcon | ' },
            propType: 'node',
            setter: {
                componentName: 'SlotSetter',
                initialValue: {
                    type: 'JSSlot',
                    value: [
                        {
                            componentName: 'AIcon',
                            props: {
                                type: 'CaretDownFilled',
                                size: 12,
                                color: '#5597f9',
                                rotate: 0,
                                spin: false,
                            }
                        }
                    ]
                }
            }
        },
        {
            name: 'tagRender',
            title: 'tagRender',
            propType: 'node',
            condition(target) {
                const mode = target.getProps().getPropValue('mode');
                return mode === 'tags' || mode === 'multiple';
            }
        },
        {
            name: 'tokenSeparators',
            title: { label: '', tip: 'tokenSeparators | ' },
            propType: { type: 'arrayOf', value: 'string' },
        },
        {
            name: 'onBlur',
            title: { label: '', tip: 'onBlur | ' },
            propType: 'func'
        },
        {
            name: 'onChange',
            title: { label: 'onChange', tip: 'onChange |  option input  value combobox ' },
            propType: 'func'
        },
        {
            name: 'onDeselect',
            title: { label: 'onDeselect', tip: 'onDeselect | ' },
            condition(target) {
                const mode = target.getProps().getPropValue('mode');
                return mode === 'tags' || mode === 'multiple';
            },
            propType: 'func'
        },
        {
            name: 'onFocus',
            title: { label: 'onFocus', tip: 'onFocus | ' },
            propType: 'func'
        },
        {
            name: 'onInputKeyDown',
            title: { label: 'onInputKeyDown', tip: 'onInputKeyDown | ' },
            propType: 'func'
        },
        {
            name: 'onMouseEnter',
            title: { label: 'onMouseEnter', tip: 'onMouseEnter | ' },
            propType: 'func'
        },
        {
            name: 'onMouseLeave',
            title: { label: 'onMouseLeave', tip: 'onMouseLeave | ' },
            propType: 'func'
        },
        {
            name: 'onPopupScroll',
            title: { label: '', tip: 'onPopupScroll | ' },
            propType: 'func'
        },
        {
            name: 'onSearch',
            title: { label: 'onSearch', tip: 'onSearch | ' },
            propType: 'func'
        },
        {
            name: 'onSelect',
            title: { label: 'onSelect', tip: 'onSelect |  value ( key) ' },
            propType: 'func'
        }
    ],
    configure: {
        supports: {
            style: true,
            events: [
                {
                    name: 'onBlur',
                    template: "onBlur(${extParams}){\n//\n console.log('onBlur')}"
                },
                {
                    name: 'onChange',
                    template: "onChange(value,option){\n console.log('onChange',value,option)}"
                },
                {
                    name: 'onDeselect',
                    template: "onDeselect(value,option){\n console.log('onDeselect',value,option)}"
                },
                {
                    name: 'onFocus',
                    template: "onFocus(${extParams}){\n//\n console.log('onFocus')}"
                }
            ]
        }
    },
    snippets: [
        {
            title: '',
            screenshot: avatarImg,
            schema: {
                componentName: 'ASelect',
                props: {
                    style: {
                        width: "220px",
                        height: '22px',
                        display: 'inline-flex',
                        justifyContent: 'center',
                        alignItems: 'center'
                    },
                    options: [
                        { label: '', value: '1' },
                        { label: '', value: '2' },
                        { label: '', value: '3' }
                    ],
                    bordered: true,
                    allowCreate: false,
                    autoFocus: false,
                    labelInValue: false,
                    placeholder: '',
                    filterOption: false,
                    autoClearSearchValue: true,
                    showArrow: true,
                    showSearch: true,
                    size: 'default',
                    tokenSeparators: [],
                    getPopupContainer: () => document.querySelector('#lce-container')
                }
            }
        },
    ],
};
