// @ts-ignore
import avatarImg from './__screenshots__/progress-1.png?inline';
// @ts-ignore
import Progress2 from './__screenshots__/progress-2.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'AProgress',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'AProgress'
    },
    props: [
        {
            name: 'type',
            title: { label: '', tip: 'type|' },
            propType: { type: 'oneOf', value: ['line', 'circle', 'dashboard'] },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        { title: '', value: 'line' },
                        { title: '', value: 'circle' },
                        { title: '', value: 'dashboard' },
                    ],
                },
            },
            defaultValue: 'line',
        },
        {
            name: 'format',
            title: { label: '', tip: 'format|' },
            propType: 'func',
        },
        {
            name: 'percent',
            title: { label: '', tip: 'percent|' },
            propType: 'number',
        },
        {
            name: 'showInfo',
            title: { label: '', tip: 'showInfo|' },
            propType: 'bool',
            defaultValue: true,
        },
        {
            name: 'status',
            title: { label: '', tip: 'status|' },
            propType: {
                type: 'oneOf',
                value: ['success', 'exception', 'normal', 'active'],
            },
        },
        {
            name: 'steps',
            title: { label: '', tip: 'steps|' },
            condition(target) {
                // 
                return target.getProps().getPropValue('type') === 'line';
            },
            propType: 'number',
        },
        {
            name: 'strokeLinecap',
            title: { label: '', tip: 'strokeLinecap|' },
            propType: {
                type: 'oneOf',
                value: ['round', 'square'],
            },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        { title: '', value: 'round' },
                        { title: '', value: 'square' },
                    ],
                },
            },
            defaultValue: 'round',
        },
        {
            name: 'strokeWidth',
            title: { label: '', tip: 'strokeWidth|' },
            propType: 'number',
        },
        {
            name: 'strokeColor',
            title: { label: '', tip: 'strokeColor|' },
            propType: 'string',
            condition(target) {
                // 
                return target.getProps().getPropValue('type') !== 'dashboard';
            },
            setter: 'ColorSetter',
        },
        {
            name: 'trailColor',
            title: {
                label: '',
                tip: 'trailColor|',
            },
            propType: 'string',
            setter: 'ColorSetter',
        },
        {
            name: 'gapDegree',
            title: {
                label: '',
                tip: 'gapDegree| 0 ~ 295',
            },
            condition(target) {
                // 
                return target.getProps().getPropValue('type') === 'dashboard';
            },
            propType: 'number',
            defaultValue: 75,
        },
        {
            name: 'gapPosition',
            title: {
                label: '',
                tip: 'gapPosition|',
            },
            condition(target) {
                // 
                return target.getProps().getPropValue('type') === 'dashboard';
            },
            propType: {
                type: 'oneOf',
                value: ['top', 'bottom', 'left', 'right'],
            },
            defaultValue: 'bottom',
        },
    ],
    configure: {
        supports: {
            style: true
        }
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "AProgress",
                props: {
                    percent: 20,
                    status: "active"
                }
            }
        },
        {
            title: "",
            screenshot: Progress2,
            schema: {
                componentName: "AProgress",
                props: {
                    percent: 20,
                    type: "circle",
                    status: "circle"
                }
            }
        }
    ]
};
