// @ts-ignore
import avatarImg from './__screenshots__/tree-select-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: "ATreeSelect",
    title: "",
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ATreeSelect'
    },
    props: [
        {
            name: 'treeData',
            title: { label: '', tip: 'treeData|' },
            setter: 'JsonSetter',
        },
        {
            name: 'defaultValue',
            title: { label: '', tip: 'defaultValue|' },
            propType: {
                type: 'oneOfType',
                value: ['string', { type: 'arrayOf', value: 'string' }],
            },
        },
        {
            name: 'value',
            title: { label: '', tip: 'value|' },
            propType: {
                type: 'oneOfType',
                value: ['string', { type: 'arrayOf', value: 'string' }],
            },
        },
        {
            name: 'allowClear',
            title: { label: '', tip: 'allowClear|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'autoClearSearchValue',
            title: {
                label: '',
                tip: 'autoClearSearchValue|',
            },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter'
        },
        {
            name: 'bordered',
            title: { label: '', tip: 'bordered|' },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter'
        },
        {
            name: 'disabled',
            title: { label: '', tip: 'disabled|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'dropdownMatchSelectWidth',
            title: {
                label: '',
                tip: 'dropdownMatchSelectWidth|',
            },
            propType: 'bool',
            setter: 'BoolSetter'
        },
        {
            name: 'filterTreeNode',
            title: {
                label: '',
                tip: 'filterTreeNode| treeNodeFilterProp  TreeNode ',
            },
            propType: {
                type: 'oneOfType',
                value: ['bool', 'func']
            },
        },
        {
            name: 'labelInValue',
            title: {
                label: '',
                tip: 'labelInValue| label  value  value  `string`  {value: string, label: ReactNode, halfChecked(treeCheckStrictly ): string[] } ',
            },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter'
        },
        {
            name: 'listHeight',
            title: { label: '', tip: 'listHeight|' },
            propType: 'number',
            setter: 'NumberSetter'
        },
        {
            name: 'loadData',
            title: { label: '', tip: 'loadData|' },
            propType: 'func',
        },
        {
            name: 'maxTagCount',
            title: { label: ' tag', tip: 'maxTagCount| tag' },
            propType: 'number',
            setter: 'NumberSetter'
        },
        {
            name: 'maxTagPlaceholder',
            title: { label: ' tag ', tip: 'maxTagPlaceholder| tag ' },
            propType: { type: 'oneOfType', value: ['node', 'func'] },
        },
        {
            name: 'multiple',
            title: {
                label: '',
                tip: 'multiple| treeCheckable  true',
            },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'placeholder',
            title: { label: '', tip: 'placeholder|' },
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            name: 'showCheckedStrategy',
            title: {
                label: '',
                tip: 'showCheckedStrategy|`SHOW_ALL`: ()`SHOW_PARENT`: () ',
            },
            propType: {
                type: 'oneOf',
                value: ['SHOW_ALL', 'SHOW_PARENT', 'SHOW_CHILD'],
            },
        },
        {
            name: 'showSearch',
            title: { label: '', tip: 'showSearch|' },
            propType: 'bool',
            setter: 'BoolSetter'
        },
        {
            name: 'size',
            title: { label: '', tip: 'size|' },
            propType: { type: 'oneOf', value: ['large', 'middle', 'small'] },
            setter: {
                componentName: 'SelectSetter',
                props: {
                    options: [
                        {
                            title: '',
                            value: 'large',
                        },
                        {
                            title: '',
                            value: 'middle',
                        },
                        {
                            title: '',
                            value: 'small',
                        },
                    ],
                },
            },
            defaultValue: 'middle',
        },
        {
            name: 'showArrow',
            title: {
                label: '',
                tip: 'showArrow| `true`',
            },
            propType: 'bool',
            setter: 'BoolSetter'
        },
        {
            name: 'treeCheckable',
            title: { label: '', tip: 'treeCheckable|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'treeDefaultExpandAll',
            title: { label: '', tip: 'treeDefaultExpandAll|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'treeDefaultExpandedKeys',
            title: { label: '', tip: 'treeDefaultExpandedKeys|' },
            propType: { type: 'arrayOf', value: 'string' },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'StringSetter',
                    }
                }
            }
        },
        {
            name: 'treeExpandedKeys',
            title: { label: '', tip: 'treeExpandedKeys|' },
            propType: { type: 'arrayOf', value: 'string' },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'StringSetter',
                    }
                }
            }
        },
        {
            name: 'virtual',
            title: {
                label: ' false ',
                tip: 'virtual| false ',
            },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter'
        },
    ],
    configure: {
        supports: {
            style: true
        },
        events: [
            {
                name: 'onChange',
                template: "onChange(value,label,extra,${extParams}){\n// \nconsole.log('onChange',value,label,extra);}",
            },
            {
                name: 'onSearch',
                template: "onSearch(value,${extParams}){\n// \nconsole.log('onSearch',value);}",
            },
            {
                name: 'onSelect',
                template: "onSelect(value,node,extra,${extParams}){\n// \nconsole.log('onSelect',value,node,extra);}",
            },
            {
                name: 'onTreeExpand',
                template: "onTreeExpand(expandedKeys,${extParams}){\n// \nconsole.log('onTreeExpand',expandedKeys);}",
            },
        ]
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "ATreeSelect",
                props: {
                    treeData: [
                        {
                            title: 'parent 0',
                            value: '0-0',
                            children: [
                                { title: 'leaf 0-0', value: '0-0-0', isLeaf: true },
                                { title: 'leaf 0-1', value: '0-0-1', isLeaf: true },
                            ],
                        },
                        {
                            title: 'parent 1',
                            value: '0-1',
                            children: [
                                { title: 'leaf 1-0', value: '0-1-0', isLeaf: true },
                                { title: 'leaf 1-1', value: '0-1-1', isLeaf: true },
                            ],
                        },
                    ],
                    treeDefaultExpandAll: true,
                    placeholder: '',
                    style: {
                        width: '300px',
                    }
                }
            }
        }
    ]
};
