// @ts-ignore
import avatarImg from './__screenshots__/calendar-1.jpg?inline';
export default {
    group: 'ant-vue',
    componentName: 'ACalendar',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ACalendar'
    },
    props: [
        {
            name: 'defaultValue',
            title: { label: '', tip: 'defaultValue|' },
            propType: 'date',
            setter: 'DateSetter',
        },
        {
            name: 'disabledDate',
            title: { label: '', tip: 'disabledDate|' },
            propType: 'func',
        },
        {
            name: 'dateCellRender',
            title: {
                label: '',
                tip: 'dateCellRender|',
            },
            propType: 'func',
        },
        {
            name: 'dateFullCellRender',
            title: {
                label: '',
                tip: 'dateFullCellRender|',
            },
            propType: 'func',
        },
        {
            name: 'fullscreen',
            title: { label: '', tip: 'fullscreen|' },
            propType: 'bool',
            defaultValue: true,
        },
        {
            name: 'mode',
            title: { label: '', tip: 'mode|' },
            propType: { type: 'oneOf', value: ['month', 'year'] },
            defaultValue: 'month',
        },
        {
            name: 'valueFormat',
            title: { label: '', tip: "valueFormat|" },
            propType: 'string',
            defaultValue: 'YYYY-MM-DD',
            setter: 'StringSetter',
        }
    ],
    configure: {
        supports: {
            style: true,
            events: [
                {
                    name: 'onPanelChange',
                    template: "onPanelChange(date,mode,${extParams}){\n// \nconsole.log('onPanelChange', date, mode);}",
                },
                {
                    name: 'onSelect',
                    template: "onSelect(date,${extParams}){\n// \nconsole.log('onSelect', date);}",
                },
                {
                    name: 'onChange',
                    template: "onChange (date,${extParams}){\n// \nconsole.log('onChange', date);}",
                },
            ],
        }
    },
    snippets: [
        {
            title: '',
            screenshot: avatarImg,
            schema: {
                componentName: "ACalendar",
                props: {}
            }
        }
    ]
};
