// @ts-ignore
import avatarImg from './__screenshots__/space-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ASpace',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ASpace'
    },
    props: [
        {
            name: 'align',
            title: { label: '', tip: 'align|' },
            propType: {
                type: 'oneOf',
                value: ['start', 'end', 'center', 'baseline'],
            },
        },
        {
            name: 'direction',
            title: { label: '', tip: 'direction|' },
            propType: { type: 'oneOf', value: ['vertical', 'horizontal'] },
        },
        {
            name: 'size',
            title: { label: '', tip: 'size|' },
            propType: {
                type: 'oneOfType',
                value: [
                    {
                        type: 'oneOf',
                        value: ['small', 'middle', 'large'],
                    },
                    'number',
                ],
            },
            defaultValue: 'middle',
        },
        {
            name: 'wrap',
            title: { label: '', tip: 'wrap|' },
            propType: "bool",
            condition: {
                type: 'JSFunction',
                value: 'target => target.getProps().getPropValue("direction")==="horizontal"',
            },
        },
        {
            name: 'split',
            title: { label: '', tip: 'split|, ' },
            propType: "node",
        },
    ],
    configure: {
        component: {
            isContainer: true
        },
        supports: {
            style: true
        }
    },
    snippets: [
        {
            title: '',
            screenshot: avatarImg,
            schema: {
                componentName: 'ASpace',
                props: {},
                children: [
                    {
                        componentName: 'AButton',
                        props: {
                            children: 'Button-lyl',
                        },
                    },
                    {
                        componentName: 'AButton',
                        props: {
                            children: 'Button-lemon',
                        },
                    },
                ],
            },
        },
    ]
};
