import { uuid } from '../../utils/index';
import { itemsExtraProps } from '../menu/util';
export default {
    group: 'ant-vue',
    componentName: 'ASubMenu',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ASubMenu'
    },
    props: [
        {
            name: 'items',
            title: { label: '', tip: 'items|' },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'ObjectSetter',
                        props: {
                            config: {
                                items: [
                                    {
                                        name: 'key',
                                        title: 'key',
                                        setter: 'StringSetter',
                                        initialValue: (val) => val || uuid(),
                                    },
                                    {
                                        name: 'children',
                                        title: '',
                                        setter: 'StringSetter',
                                    },
                                    {
                                        name: 'category',
                                        title: {
                                            label: '',
                                            tip: '',
                                        },
                                        propType: {
                                            type: 'oneOf',
                                            value: ['AMenuItem', 'ASubMenu', 'AMenuItemGroup'],
                                        },
                                        setter: [
                                            {
                                                componentName: 'RadioGroupSetter',
                                                props: {
                                                    options: [
                                                        {
                                                            title: 'Item',
                                                            value: 'AMenuItem',
                                                        },
                                                        {
                                                            title: 'SubMenu',
                                                            value: 'ASubMenu',
                                                        },
                                                        {
                                                            title: 'ItemGroup',
                                                            value: 'AMenuItemGroup',
                                                        },
                                                    ],
                                                },
                                            },
                                            'VariableSetter',
                                        ],
                                    },
                                ],
                            },
                        },
                        initialValue: () => {
                            return {
                                key: `item-${uuid()}`,
                                category: 'AMenuItem',
                                children: '',
                            };
                        },
                    },
                },
            },
            extraProps: itemsExtraProps,
        },
        {
            name: 'danger',
            title: { label: '', tip: 'danger|' },
            propType: 'bool',
            defaultValue: true,
        },
        {
            name: 'disabled',
            title: { label: '', tip: 'disabled|' },
            propType: 'bool',
            defaultValue: false,
        },
        {
            name: 'icon',
            title: { label: '', tip: 'icon|' },
            propType: { type: 'oneOfType', value: ['string', 'node'] },
            defaultValue: false,
        },
        {
            name: 'key',
            title: { label: '', tip: 'key|item ' },
            propType: 'string',
        },
        {
            name: 'title',
            title: {
                label: '',
                tip: 'title|',
            },
            propType: 'string',
        },
    ],
    configure: {
        component: {
            isContainer: true,
            nestingRule: { parentWhitelist: ['AMenu'] }
        },
        supports: {
            style: true
        }
    },
    snippets: []
};
