// @ts-ignore
import avatarImg from './__screenshots__/tooltip-1.jpg?inline';
export default {
    group: 'ant-vue',
    componentName: 'ATooltip',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ATooltip'
    },
    props: [
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'title',
                    title: {
                        label: '',
                        tip: 'title | ',
                    },
                    propType: { type: 'oneOfType', value: ['string', 'node'] },
                    setter: ['StringSetter', 'SlotSetter', 'VariableSetter'],
                },
            ],
        },
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'defaultVisible',
                    title: {
                        label: '',
                        tip: 'defaultVisible | ',
                    },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'visible',
                    title: {
                        label: '',
                        tip: 'visible | ',
                    },
                    propType: 'bool',
                    setter: 'BoolSetter',
                },
            ],
        },
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'placement',
                    title: { label: '', tip: 'placement | ' },
                    propType: {
                        type: 'oneOf',
                        value: [
                            'top',
                            'left',
                            'right',
                            'bottom',
                            'topLeft',
                            'topRight',
                            'bottomLeft',
                            'bottomRight',
                            'leftTop',
                            'leftBottom',
                            'rightTop',
                            'rightBottom',
                        ],
                    },
                    defaultValue: 'top',
                    setter: {
                        componentName: 'SelectSetter',
                        props: {
                            options: [
                                {
                                    title: '',
                                    value: 'top',
                                },
                                {
                                    title: '',
                                    value: 'left',
                                },
                                {
                                    title: '',
                                    value: 'right',
                                },
                                {
                                    title: '',
                                    value: 'bottom',
                                },
                                {
                                    title: '',
                                    value: 'topLeft',
                                },
                                {
                                    title: '',
                                    value: 'topRight',
                                },
                                {
                                    title: '',
                                    value: 'bottomLeft',
                                },
                                {
                                    title: '',
                                    value: 'bottomRight',
                                },
                                {
                                    title: '',
                                    value: 'leftTop',
                                },
                                {
                                    title: '',
                                    value: 'leftBottom',
                                },
                                {
                                    title: '',
                                    value: 'rightTop',
                                },
                                {
                                    title: '',
                                    value: 'rightBottom',
                                },
                            ],
                        },
                    },
                },
                {
                    name: 'autoAdjustOverflow',
                    title: {
                        label: '',
                        tip: 'autoAdjustOverflow | ',
                    },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: true,
                },
                {
                    name: 'arrowPointAtCenter',
                    title: {
                        label: '',
                        tip: 'arrowPointAtCenter | ',
                    },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'color',
                    title: {
                        label: '',
                        tip: 'color | ',
                    },
                    propType: 'string',
                    setter: 'ColorSetter',
                },
                {
                    name: 'zIndex',
                    title: {
                        label: 'zIndex',
                        tip: 'zIndex |  Tooltip  z-index',
                    },
                    propType: 'number',
                    setter: 'NumberSetter',
                },
            ],
        },
        {
            name: 'overlayStyle',
            title: '',
            type: 'group',
            extraProps: {
                display: 'entry',
            },
            items: [
                {
                    name: 'overlayStyle',
                    title: { label: '', tip: 'overlayStyle | ' },
                    setter: 'StyleSetter',
                    extraProps: {
                        display: 'block',
                    },
                },
            ],
        },
        {
            name: 'overlayInnerStyle',
            title: { label: '', tip: 'overlayInnerStyle | ' },
            type: 'group',
            extraProps: {
                display: 'entry',
            },
            items: [
                {
                    name: 'overlayInnerStyle',
                    title: {
                        label: '',
                        tip: 'overlayStyle | ',
                    },
                    setter: 'StyleSetter',
                    extraProps: {
                        display: 'block',
                    },
                },
            ],
        },
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'trigger',
                    title: { label: '', tip: 'trigger | ' },
                    propType: {
                        type: 'oneOf',
                        value: ['hover', 'onFocus', 'click', 'contextMenu'],
                    },
                    defaultValue: 'hover',
                    setter: {
                        componentName: 'SelectSetter',
                        props: {
                            options: [
                                {
                                    title: '',
                                    value: 'hover',
                                },
                                {
                                    title: '',
                                    value: 'onFocus',
                                },
                                {
                                    title: '',
                                    value: 'click',
                                },
                                {
                                    title: '',
                                    value: 'contextMenu',
                                },
                            ],
                        },
                    },
                },
                {
                    name: 'mouseEnterDelay',
                    title: {
                        label: '',
                        tip: 'mouseEnterDelay |  Tooltip',
                    },
                    propType: 'number',
                    defaultValue: 0.1,
                    setter: {
                        componentName: 'NumberSetter',
                        props: {
                            step: 0.1,
                        },
                    },
                },
                {
                    name: 'mouseLeaveDelay',
                    title: {
                        label: '',
                        tip: 'mouseLeaveDelay |  Tooltip',
                    },
                    propType: 'number',
                    defaultValue: 0.1,
                    setter: {
                        componentName: 'NumberSetter',
                        props: {
                            step: 0.1,
                        },
                    },
                },
            ],
        }
    ],
    configure: {
        component: { isContainer: true },
        supports: {
            style: true
        },
        events: [
            {
                name: 'onVisibleChange',
                template: "onVisibleChange(visible,${extParams}){\n// \nconsole.log('onVisibleChange',visible);}",
            }
        ]
    },
    snippets: [
        {
            title: '',
            screenshot: avatarImg,
            schema: {
                componentName: 'ATooltip',
                props: {
                    title: '',
                }
            }
        }
    ]
};
