// @ts-ignore
import avatarImg from './__screenshots__/upload-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'AUpload',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'AUpload'
    },
    props: [
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'defaultFileList',
                    title: {
                        label: '',
                        tip: 'defaultFileList | ',
                    },
                    propType: { type: 'arrayOf', value: 'object' },
                    setter: {
                        componentName: 'ArraySetter',
                        props: {
                            itemSetter: {
                                componentName: 'ObjectSetter',
                                props: {
                                    config: {
                                        items: [
                                            {
                                                name: 'name',
                                                title: '',
                                                setter: 'StringSetter',
                                            },
                                            {
                                                name: 'status',
                                                title: '',
                                                setter: {
                                                    componentName: 'SelectSetter',
                                                    props: {
                                                        options: [
                                                            'error',
                                                            'success',
                                                            'done',
                                                            'uploading',
                                                            'removed',
                                                        ].map((v) => ({ label: v, value: v })),
                                                    },
                                                },
                                            },
                                            {
                                                name: 'url',
                                                title: '',
                                                setter: 'StringSetter',
                                            },
                                            {
                                                name: 'url',
                                                title: '',
                                                setter: 'StringSetter',
                                            },
                                            {
                                                name: 'response',
                                                title: '',
                                                setter: 'StringSetter',
                                            },
                                        ],
                                    },
                                },
                            },
                        },
                    },
                },
                {
                    name: 'fileList',
                    title: {
                        label: '',
                        tip: 'fileList | ',
                    },
                    propType: { type: 'arrayOf', value: 'object' },
                    setter: {
                        componentName: 'ArraySetter',
                        props: {
                            itemSetter: {
                                componentName: 'ObjectSetter',
                                props: {
                                    config: {
                                        items: [
                                            {
                                                name: 'name',
                                                title: '',
                                                setter: 'StringSetter',
                                            },
                                            {
                                                name: 'status',
                                                title: '',
                                                setter: {
                                                    componentName: 'SelectSetter',
                                                    props: {
                                                        options: [
                                                            'error',
                                                            'success',
                                                            'done',
                                                            'uploading',
                                                            'removed',
                                                        ].map((v) => ({ label: v, value: v })),
                                                    },
                                                },
                                            },
                                            {
                                                name: 'url',
                                                title: '',
                                                setter: 'StringSetter',
                                            },
                                            {
                                                name: 'url',
                                                title: '',
                                                setter: 'StringSetter',
                                            },
                                            {
                                                name: 'response',
                                                title: '',
                                                setter: 'StringSetter',
                                            },
                                        ],
                                    },
                                },
                            },
                        },
                    },
                },
                {
                    name: 'multiple',
                    title: {
                        label: '',
                        tip: 'multiple | `ie10+`  ctrl ',
                    },
                    propType: 'bool',
                    defaultValue: false,
                    setter: 'BoolSetter',
                },
                {
                    name: 'maxCount',
                    title: {
                        label: '',
                        tip: 'maxCount |  1 ',
                    },
                    propType: 'number',
                    setter: 'NumberSetter',
                },
                {
                    name: 'accept',
                    title: {
                        label: '',
                        tip: 'accept | ,  .doc,.docx,application/msword',
                    },
                    propType: 'string',
                    setter: 'StringSetter',
                },
                {
                    name: 'directory',
                    title: {
                        label: '',
                        tip: 'directory | ',
                    },
                    propType: 'bool',
                    defaultValue: false,
                    setter: 'BoolSetter',
                },
                {
                    name: 'disabled',
                    title: { label: '', tip: 'disabled | ' },
                    propType: 'bool',
                    defaultValue: false,
                    setter: 'BoolSetter',
                },
            ],
        },
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'openFileDialogOnClick',
                    title: {
                        label: '',
                        tip: 'openFileDialogOnClick | ',
                    },
                    propType: 'bool',
                    defaultValue: true,
                    setter: 'BoolSetter',
                },
                {
                    name: 'showUploadList',
                    title: {
                        label: '',
                        tip: 'showUploadList | ,',
                    },
                    propType: 'bool',
                    defaultValue: true,
                    setter: 'BoolSetter',
                },
                {
                    name: 'listType',
                    title: {
                        label: '',
                        tip: 'listType |  `text`, `picture`  `picture-card`',
                    },
                    propType: {
                        type: 'oneOf',
                        value: ['text', 'picture', 'picture-card'],
                    },
                    defaultValue: 'text',
                    setter: [
                        {
                            componentName: 'RadioGroupSetter',
                            props: {
                                options: [
                                    {
                                        title: '',
                                        value: 'text',
                                    },
                                    {
                                        title: '',
                                        value: 'picture',
                                    },
                                    {
                                        title: '',
                                        value: 'picture-card',
                                    },
                                ],
                            },
                        },
                        'VariableSetter',
                    ],
                },
                {
                    name: 'iconRender',
                    title: { label: '', tip: 'iconRender |  icon' },
                    propType: 'func',
                    setter: {
                        componentName: 'SlotSetter',
                        title: '',
                        initialValue: {
                            type: 'JSSlot',
                            params: ['file', 'listType'],
                            value: [],
                        },
                    },
                },
                {
                    name: 'itemRender',
                    title: {
                        label: '',
                        tip: 'itemRender | ',
                    },
                    propType: 'func',
                    setter: {
                        componentName: 'SlotSetter',
                        title: '',
                        initialValue: {
                            type: 'JSSlot',
                            params: ['originNode', 'file', 'fileList', 'actions'],
                            value: [],
                        },
                    },
                },
                {
                    name: 'progress',
                    title: { label: '', tip: 'progress | ' },
                    propType: 'object',
                    setter: 'JsonSetter',
                },
            ],
        },
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'action',
                    title: { label: '', tip: 'action | ' },
                    propType: { type: 'oneOfType', value: ['string', 'func'] },
                    setter: ['StringSetter', 'FunctionSetter', 'VariableSetter'],
                },
                {
                    name: 'name',
                    title: { label: '', tip: 'name | ' },
                    propType: 'string',
                    setter: 'StringSetter',
                },
                {
                    name: 'method',
                    title: {
                        label: 'Method',
                        tip: 'method |  http method',
                    },
                    propType: {
                        type: 'oneOf',
                        value: ['get', 'post', 'put', 'head', 'options', 'patch', 'delete'],
                    },
                    defaultValue: 'post',
                    setter: 'StringSetter',
                },
                {
                    name: 'headers',
                    title: {
                        label: '',
                        tip: 'headers | IE10 ',
                    },
                    propType: 'object',
                    setter: 'JsonSetter',
                },
                {
                    name: 'withCredentials',
                    title: {
                        label: 'Cookie',
                        tip: 'withCredentials |  cookie',
                    },
                    propType: 'bool',
                    defaultValue: false,
                    setter: 'BoolSetter',
                },
                {
                    name: 'data',
                    title: {
                        label: '',
                        tip: 'data | ',
                    },
                    propType: { type: 'oneOfType', value: ['object', 'func'] },
                    setter: [
                        'JsonSetter',
                        {
                            componentName: 'FunctionSetter',
                            props: {
                                template: 'onData(file,${extParams}){\n// \nreturn {};\n}',
                            },
                        },
                        'VariableSetter',
                    ],
                },
            ],
        },
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'beforeUpload',
                    title: {
                        label: '',
                        tip: 'beforeUpload | ',
                    },
                    propType: 'func',
                    setter: {
                        componentName: 'FunctionSetter',
                        props: {
                            template: 'beforeUpload(file,fileList,${extParams}){\n// \nreturn false;\n}',
                        },
                    },
                },
                {
                    name: 'customRequest',
                    title: {
                        label: '',
                        tip: 'customRequest | ',
                    },
                    propType: 'func',
                    setter: 'FunctionSetter',
                },
                {
                    name: 'isImageUrl',
                    title: {
                        label: '',
                        tip: 'isImageUrl |  <img /> ',
                    },
                    propType: 'func',
                    setter: {
                        componentName: 'FunctionSetter',
                        props: {
                            template: 'isImageUrl(file,${extParams}){\n// \nreturn true;\n}',
                        },
                    },
                },
                {
                    name: 'previewFile',
                    title: {
                        label: '',
                        tip: 'previewFile | ',
                    },
                    propType: 'func',
                    setter: 'FunctionSetter',
                },
            ],
        },
    ],
    configure: {
        component: { isContainer: true },
        supports: {
            style: true
        },
        events: [
            {
                name: 'onChange',
                template: "onChange ({file,fileList,event},${extParams}){\n// \nconsole.log('onChange ',file,fileList,event);}",
            },
            {
                name: 'onPreview',
                template: "onPreview(file,${extParams}){\n// \nconsole.log('onPreview',file);}",
            },
            {
                name: 'onRemove',
                template: "onRemove(file,${extParams}){\n// \nconsole.log('onRemove',file);}",
            },
            {
                name: 'onDownload',
                template: "onDownload(file,${extParams}){\n// \nconsole.log('onDownload',file);}",
            },
            {
                name: 'onDrop',
                template: "onDrop(event,${extParams}){\n// \nconsole.log('onDrop',event);}"
            }
        ]
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "AUpload",
                props: {},
                children: {
                    componentName: "AButton",
                    props: {
                        children: "AUpload"
                    }
                }
            }
        }
    ]
};
