// @ts-ignore
import avatarImg from './__screenshots__/rate-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ARate',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ARate'
    },
    props: [
        {
            name: 'value',
            title: { label: '', tip: 'value|' },
            propType: 'number',
            setter: 'NumberSetter',
            defaultValue: 1
        },
        {
            name: 'allowClear',
            title: { label: '', tip: 'allowClear|' },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter'
        },
        {
            name: 'allowHalf',
            title: { label: '', tip: '' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'autoFocus',
            title: { label: '', tip: 'autoFocus|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'character',
            title: { label: '', tip: 'character|' },
            propType: { type: 'oneOfType', value: ['string', 'node'] }
        },
        {
            name: 'count',
            title: { label: 'star', tip: 'count|star ' },
            propType: 'number',
            setter: 'NumberSetter',
            defaultValue: 5
        },
        {
            name: 'disabled',
            title: { label: '', tip: 'disabled|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'tooltips',
            title: { label: '', tip: 'tooltips|' },
            propType: {
                type: 'arrayOf', value: 'string'
            }
        }
    ],
    configure: {
        supports: {
            style: true,
            events: [
                {
                    name: 'onChange ',
                    template: "onChange (value,${extParams}){\n// \nconsole.log('onChange ',value);}",
                },
                {
                    name: 'onHoverChange',
                    template: "onHoverChange(value,${extParams}){\n// \nconsole.log('onHoverChange',value);}",
                },
                {
                    name: 'onKeyDown',
                    template: "onKeyDown(event,${extParams}){\n// \nconsole.log('onKeyDown',event);}",
                },
                {
                    name: 'onFocus',
                    template: "onFocus(${extParams}){\n// \nconsole.log('onFocus');}",
                },
                {
                    name: 'onBlur',
                    template: "onBlur(${extParams}){\n// \nconsole.log('onBlur');}",
                },
            ],
        }
    },
    snippets: [
        {
            title: '',
            screenshot: avatarImg,
            schema: {
                componentName: 'ARate',
                props: {
                    value: 3
                }
            }
        }
    ]
};
