// @ts-ignore
import avatarImg from './__screenshots__/breadcrumb-1.jpg?inline';
export default {
    group: 'ant-vue',
    componentName: 'ABreadcrumb',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ABreadcrumb'
    },
    props: [
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'routes',
                    title: { label: '', tip: 'routes|router ' },
                    propType: {
                        type: 'arrayOf',
                        value: {
                            type: 'shape',
                            value: [
                                { name: 'path', propType: 'string' },
                                { name: 'breadcrumbName', propType: 'string' },
                            ],
                        },
                    },
                    setter: {
                        componentName: 'ArraySetter',
                        props: {
                            itemSetter: {
                                componentName: 'ObjectSetter',
                                props: {
                                    config: {
                                        items: [
                                            {
                                                name: 'path',
                                                title: { label: '', tip: 'path | ' },
                                                propType: 'string',
                                                setter: 'StringSetter',
                                                isRequired: true
                                            },
                                            {
                                                name: 'breadcrumbName',
                                                title: {
                                                    label: '',
                                                    tip: 'breadcrumbName | ',
                                                },
                                                propType: 'string',
                                                setter: 'StringSetter',
                                                isRequired: true
                                            },
                                        ],
                                    },
                                },
                                initialValue: {
                                    path: 'path',
                                    breadcrumbName: 'breadcrumbName',
                                },
                            },
                        },
                    },
                },
                {
                    name: 'params',
                    title: { label: '', tip: 'params|' },
                    propType: 'object',
                    setter: 'JsonSetter',
                },
                {
                    name: 'separator',
                    title: { label: '', tip: 'separator|' },
                    propType: { type: 'oneOfType', value: ['string', 'node'] },
                    setter: [
                        'StringSetter',
                        {
                            componentName: 'SlotSetter',
                            initialValue: {
                                type: 'JSSlot',
                                value: [],
                            },
                        },
                        'VariableSetter',
                    ],
                },
            ]
        },
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'itemRender',
                    title: {
                        label: '',
                        tip: 'itemRender | ',
                    },
                    propType: { type: 'oneOfType', value: ['func', 'node'] },
                    setter: [
                        {
                            componentName: 'SlotSetter',
                            title: '',
                            initialValue: {
                                type: 'JSSlot',
                                params: ['route', 'params', 'routes', 'paths'],
                                value: [],
                            },
                        },
                        {
                            componentName: 'FunctionSetter',
                            props: {
                                template: 'itemRender(route, params, routes, paths,${extParams}){\n// \nreturn `${route.breadcrumbName}`}',
                            },
                        },
                        'VariableSetter',
                    ],
                }
            ]
        }
    ],
    configure: {
        supports: {
            style: true
        }
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "ABreadcrumb",
                props: {
                    routes: [
                        {
                            path: "home",
                            breadcrumbName: "Home"
                        },
                        {
                            path: "order",
                            breadcrumbName: "Order"
                        },
                        {
                            path: "app",
                            breadcrumbName: "An application"
                        }
                    ],
                    itemRender: {
                        type: 'JSSlot',
                        params: ['route', 'params', 'routes', 'paths'],
                        value: [
                            {
                                componentName: 'ATypographyLink',
                                props: {
                                    href: {
                                        type: 'JSExpression',
                                        value: 'this.route.path',
                                    },
                                    children: {
                                        type: 'JSExpression',
                                        value: 'this.route.breadcrumbName',
                                    },
                                },
                            },
                        ],
                    }
                }
            }
        }
    ]
};
