/* eslint-disable */
// @ts-ignore
import avatarImg from './__screenshots__/cascader-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ACascader',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ACascader'
    },
    props: [
        {
            name: 'defaultValue',
            title: { label: '', tip: 'defaultValue|' },
            propType: {
                type: 'arrayOf',
                value: { type: 'oneOfType', value: ['string', 'number'] },
            },
        },
        {
            name: 'value',
            title: { label: '', tip: 'value|' },
            propType: {
                type: 'arrayOf',
                value: { type: 'oneOfType', value: ['string', 'number'] },
            },
        },
        {
            name: 'options',
            title: { label: '', tip: 'options|' },
            setter: 'JsonSetter',
        },
        {
            name: 'allowClear',
            title: { label: '', tip: 'allowClear|' },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter'
        },
        {
            name: 'autoFocus',
            title: { label: '', tip: 'autoFocus|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'bordered',
            title: { label: '', tip: 'bordered|' },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter'
        },
        {
            name: 'changeOnSelect',
            title: {
                label: '',
                tip: 'changeOnSelect|onChange',
            },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'disabled',
            title: { label: '', tip: 'disabled|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'expandTrigger',
            title: { label: '', tip: 'expandTrigger|' },
            propType: { type: 'oneOf', value: ['click', 'hover'] },
        },
        {
            name: 'notFoundContent',
            title: { label: '', tip: 'notFoundContent|' },
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            name: 'placeholder',
            title: { label: '', tip: 'placeholder|' },
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            name: 'placement',
            title: { label: '', tip: 'placement|' },
            propType: {
                type: 'oneOf',
                value: ['bottomLeft', 'bottomRight', 'topLeft', 'topRight'],
            },
            defaultValue: 'bottomLeft'
        },
        {
            name: 'searchValue',
            title: { label: ' showSearch ', tip: 'searchValue' },
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            name: 'showSearch',
            title: { label: '', tip: 'showSearch|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'size',
            title: { label: '', tip: 'size|' },
            propType: { type: 'oneOf', value: ['large', 'middle', 'small'] },
            setter: {
                componentName: 'SelectSetter',
                props: {
                    options: [
                        {
                            title: '',
                            value: 'large',
                        },
                        {
                            title: '',
                            value: 'middle',
                        },
                        {
                            title: '',
                            value: 'small',
                        },
                    ],
                },
            },
            defaultValue: 'middle',
        },
        {
            name: 'multiple',
            title: { label: '', tip: 'multiple|' },
            propType: 'bool',
            setter: 'BoolSetter',
            defaultValue: false
        },
        {
            name: 'tagRender',
            title: { label: ' tag ', tip: 'tagRender' },
            condition: (target) => {
                return target.getProps().getPropValue('multiple') === true;
            },
            setter: [
                {
                    componentName: 'SlotSetter',
                    title: '',
                    initialValue: {
                        type: 'JSSlot',
                        params: ['value', 'label'],
                        value: [],
                    },
                },
            ]
        },
        {
            name: 'popupVisible',
            title: { label: '', tip: 'popupVisible' },
            propType: 'bool',
            setter: 'BoolSetter',
            supportVariable: true
        },
        {
            name: 'onChange',
            title: { label: '', tip: 'onChange | ' },
            propType: 'func'
        },
        {
            name: 'onPopupVisibleChange',
            title: { label: '', tip: 'onPopupVisibleChange|' },
            propType: 'func'
        },
        {
            name: 'onSearch',
            title: { label: 'onSearch', tip: 'onSearch|' },
            propType: 'func'
        }
    ],
    configure: {
        supports: {
            style: true,
            events: [
                {
                    name: 'onChange',
                    template: "onChange(value,selectedOptions,${extParams}){\n//\nconsole.log('onChange',value,selectedOptions);}"
                },
                {
                    name: 'onPopupVisibleChange',
                    template: "onPopupVisibleChange(value){\n///\nconsole.log('onPopupVisibleChange',value);}"
                },
                {
                    name: 'onSearch',
                    template: "onSearch(value){\n//\nconsole.log('onSearch',value);}"
                }
            ]
        }
    },
    snippets: {
        title: '',
        screenshot: avatarImg,
        schema: {
            componentName: 'ACascader',
            props: {
                options: [
                    {
                        value: 'zhejiang',
                        label: 'Zhejiang',
                        children: [
                            {
                                value: 'hangzhou',
                                label: 'Hangzhou',
                                children: [
                                    {
                                        value: 'xihu',
                                        label: 'West Lake',
                                    },
                                ],
                            },
                        ],
                    },
                    {
                        value: 'jiangsu',
                        label: 'Jiangsu',
                        children: [
                            {
                                value: 'nanjing',
                                label: 'Nanjing',
                                children: [
                                    {
                                        value: 'zhonghuamen',
                                        label: 'Zhong Hua Men',
                                    },
                                ],
                            },
                        ],
                    },
                ],
                placeholder: ''
            }
        }
    }
};
