// @ts-ignore
import avatarImg from './__screenshots__/affix-1.jpg?inline';
export default {
    group: 'ant-vue',
    componentName: 'AAffix',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'AAffix'
    },
    props: [
        {
            name: 'offsetBottom',
            title: { label: '', tip: 'offsetBottom | ' },
            propType: 'number',
        },
        {
            name: 'offsetTop',
            title: { label: '', tip: 'offsetTop | ' },
            propType: 'number',
        },
        {
            name: 'target',
            title: {
                label: '',
                tip: 'target| `Affix`  DOM ',
            },
            propType: 'func',
        }
    ],
    configure: {
        supports: {
            style: true,
            events: [
                {
                    name: 'onChange ',
                    template: "onChange (affixed,${extParams}){\n// \nconsole.log('onChange ', affixed);}",
                },
            ],
        }
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "AAffix",
                props: {
                    offsetTop: 100,
                },
                children: [
                    {
                        componentName: "AButton",
                        props: {
                            children: "Affix Top"
                        }
                    }
                ]
            }
        }
    ]
};
