// @ts-ignore
import avatarImg from './__screenshots__/time-picker-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ATimePicker',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ATimePicker'
    },
    props: [
        {
            name: 'defaultValue',
            title: { label: '', tip: 'defaultValue|' },
            propType: 'date',
            setter: 'DateSetter',
        },
        {
            name: 'value',
            title: { label: '', tip: 'value|' },
            propType: 'date',
            setter: 'DateSetter',
        },
        {
            name: 'allowClear',
            title: { label: '', tip: 'allowClear|' },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter'
        },
        {
            name: 'autoFocus',
            title: { label: '', tip: 'autoFocus|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'bordered',
            title: { label: '', tip: 'bordered|' },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter'
        },
        {
            name: 'clearText',
            title: { label: '', tip: 'clearText|' },
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            name: 'disabled',
            title: { label: '', tip: 'disabled|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'disabledHours',
            title: { label: '', tip: 'disabledHours|' },
            propType: 'func',
        },
        {
            name: 'disabledMinutes',
            title: { label: '', tip: 'disabledMinutes|' },
            propType: 'func',
        },
        {
            name: 'disabledSeconds',
            title: { label: '', tip: 'disabledSeconds|' },
            propType: 'func',
        },
        {
            name: 'format',
            title: { label: '', tip: 'format|' },
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            name: 'getPopupContainer',
            title: {
                label: '',
                tip: 'getPopupContainer| body  div',
            },
            propType: 'func',
        },
        {
            name: 'hideDisabledOptions',
            title: { label: '', tip: 'hideDisabledOptions|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'hourStep',
            title: { label: '', tip: 'hourStep|' },
            propType: 'number',
            setter: 'NumberSetter'
        },
        {
            name: 'inputReadOnly',
            title: {
                label: '',
                tip: 'inputReadOnly|',
            },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'minuteStep',
            title: { label: '', tip: 'minuteStep|' },
            propType: 'number',
            setter: 'NumberSetter'
        },
        {
            name: 'placeholder',
            title: { label: '', tip: 'placeholder|' },
            propType: {
                type: 'oneOfType',
                value: ['string', { type: 'arrayOf', value: 'string' }],
            },
        },
        {
            name: 'popupClassName',
            title: { label: '', tip: 'popupClassName|' },
            propType: 'string',
            setter: 'StringSetter'
        },
        {
            name: 'secondStep',
            title: { label: '', tip: 'secondStep|' },
            propType: 'number',
            setter: 'NumberSetter'
        },
        {
            name: 'use12Hours',
            title: {
                label: '12',
                tip: 'use12Hours| 12  true  `format`  `h:mm:ss a`',
            },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'showNow',
            title: {
                label: '',
                tip: 'showNow|'
            },
            propType: 'bool',
            setter: 'BoolSetter'
        }
    ],
    configure: {
        supports: {
            style: true
        },
        events: [
            {
                name: 'onChange',
                template: "onChange (time,timeString,${extParams}){\n// \nconsole.log('onChange ',time,timeString);}",
            },
            {
                name: 'onOpenChange',
                template: "onOpenChange(open,${extParams}){\n// /\nconsole.log('onOpenChange',open);}",
            }
        ]
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "ATimePicker",
                props: {
                    showNow: true,
                    getPopupContainer: () => document.querySelector('#lce-container')
                }
            }
        }
    ]
};
