/* eslint-disable */
// @ts-ignore
import avatarImg from './__screenshots__/pagination-1.jpg?inline';
export default {
    group: 'ant-vue',
    componentName: 'APagination',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'APagination'
    },
    props: [
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'defaultPageSize',
                    title: {
                        label: '',
                        tip: 'defaultPageSize | ',
                    },
                    propType: 'number',
                    setter: 'NumberSetter',
                    defaultValue: 10,
                },
                {
                    name: 'pageSize',
                    title: { label: '', tip: 'pageSize | ' },
                    propType: 'number',
                    setter: 'NumberSetter',
                    defaultValue: 10,
                },
                {
                    name: 'total',
                    title: { label: '', tip: 'total | ' },
                    propType: 'number',
                    setter: 'NumberSetter',
                    defaultValue: 15,
                },
                {
                    name: 'defaultCurrent',
                    title: {
                        label: '',
                        tip: 'defaultCurrent | ',
                    },
                    propType: 'number',
                    setter: 'NumberSetter',
                    defaultValue: 1,
                },
                {
                    name: 'current',
                    title: { label: '', tip: 'current | ' },
                    propType: 'number',
                    setter: 'NumberSetter',
                    defaultValue: 1,
                },
            ],
        },
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'disabled',
                    title: {
                        label: '',
                        tip: 'disabled | ',
                    },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'showSizeChanger',
                    title: {
                        label: '',
                        tip: 'showSizeChanger |  pageSize ',
                    },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'showQuickJumper',
                    title: {
                        label: '',
                        tip: 'showQuickJumper | ',
                    },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'hideOnSinglePage',
                    title: {
                        label: '',
                        tip: 'hideOnSinglePage | ',
                    },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'showLessItems',
                    title: {
                        label: '',
                        tip: 'showLessItems | ',
                    },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'pageSizeOptions',
                    title: {
                        label: '',
                        tip: 'pageSizeOptions |  pageSize ',
                    },
                    propType: 'object',
                    setter: 'JsonSetter',
                },
            ],
        },
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'simple',
                    title: { label: '', tip: 'simple | ' },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'size',
                    title: { label: '', tip: 'size | ' },
                    propType: {
                        type: 'oneOf',
                        value: ['default', 'small'],
                    },
                    setter: [
                        {
                            componentName: 'RadioGroupSetter',
                            props: {
                                options: [
                                    {
                                        title: '',
                                        value: 'default',
                                    },
                                    {
                                        title: '',
                                        value: 'small',
                                    },
                                ],
                            },
                        },
                        'VariableSetter',
                    ],
                    defaultValue: 'default',
                },
                {
                    name: 'showTotal',
                    title: {
                        label: '',
                        tip: 'showTotal | ',
                    },
                    propType: 'func',
                    setter: [
                        {
                            componentName: 'FunctionSetter',
                            props: {
                                template: 'showTotal(total,range,${extParams}){\n// \nreturn ` ${total} `;\n}',
                            },
                        },
                        'VariableSetter',
                    ],
                },
                {
                    name: 'showTitle',
                    title: {
                        label: '',
                        tip: 'showTitle |  tooltip ',
                    },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'responsive',
                    title: {
                        label: '',
                        tip: 'responsive |  size ',
                    },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
            ],
        },
    ],
    configure: {
        supports: {
            style: true,
            events: [
                { name: 'onChange', template: 'onChange (page,pageSize){\n// pageSize \nconsole.log("onChange ",page,pageSize);}' },
                { name: 'onShowSizeChange', template: 'onShowSizeChange(current,size){\n//pageSize \nconsole.log("onShowSizeChange",current,size);}' }
            ],
        }
    },
    snippets: [
        {
            title: '',
            screenshot: avatarImg,
            schema: {
                componentName: 'APagination',
                style: {
                    width: '100%',
                    height: '36px',
                    display: 'flex',
                    alignItems: 'center',
                    backgroundColor: '#fff',
                    borderTop: '1px solid #EBEEF5',
                    padding: '0 10px'
                },
                props: {
                    pageSize: 10,
                    total: 50,
                    current: 1,
                    showSizeChanger: true,
                    hideOnSinglePage: false,
                    showQuickJumper: true
                }
            }
        }
    ]
};
