/* eslint-disable */
// @ts-ignore
import avatarImg from './__screenshots__/radio-group-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ARadioGroup',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ARadioGroup'
    },
    props: [
        {
            name: 'v-model:value',
            title: { label: '', tip: 'v-model:value|' },
            propType: 'string',
            setter: 'StringSetter',
            supportVariable: true
        },
        {
            name: 'name',
            title: { label: 'name', tip: 'name|name' },
            propType: 'string',
            setter: 'StringSetter',
            supportVariable: true
        },
        {
            name: 'options',
            propType: {
                type: 'arrayOf',
                value: {
                    type: 'shape',
                    value: [
                        {
                            name: 'label',
                            propType: 'string',
                            title: { label: '', tip: 'label|' },
                            defaultValue: '',
                            supportVariable: true
                        },
                        {
                            name: 'value',
                            propType: 'string',
                            title: { label: '', tip: 'value|' },
                            defaultValue: '',
                            supportVariable: true
                        },
                        {
                            name: 'disabled',
                            propType: 'bool',
                            title: { label: '', tip: 'disabled|' },
                            defaultValue: false,
                            supportVariable: true
                        },
                    ],
                },
            },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'ObjectSetter',
                        props: {
                            config: {
                                items: [
                                    {
                                        name: 'label',
                                        title: '',
                                        setter: 'StringSetter',
                                        isRequired: true
                                    },
                                    {
                                        name: 'value',
                                        title: '',
                                        setter: 'StringSetter',
                                        isRequired: true
                                    },
                                    {
                                        name: 'disabled',
                                        title: '',
                                        setter: 'BoolSetter',
                                    },
                                ],
                            },
                        },
                        initialValue: () => {
                            return {
                                label: '',
                                value: '',
                                disabled: false,
                            };
                        },
                    },
                },
            },
        },
        {
            name: 'buttonStyle',
            title: { label: 'buttonStyle', tip: 'buttonStyle|RadioButton ' },
            propType: { type: 'oneOf', value: ['outline', 'solid'] },
            defaultValue: 'outline',
            condition(target) {
                return target.getProps().getPropValue('optionType') === 'button';
            },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        { label: 'outline', value: 'outline' },
                        { label: 'solid', value: 'solid' },
                    ],
                },
            }
        },
        {
            name: 'disabled',
            title: { label: '', tip: 'disabled|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter',
            supportVariable: true
        },
        {
            name: 'optionType',
            title: { label: '', tip: 'optionType|' },
            propType: { type: 'oneOf', value: ['default', 'button'] },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        {
                            title: '',
                            value: 'default',
                        },
                        {
                            title: '',
                            value: 'button',
                        },
                    ],
                },
            },
            defaultValue: 'default',
        },
        {
            name: 'size',
            title: { label: '', tip: 'size|' },
            condition(target) {
                return target.getProps().getPropValue('optionType') === 'button';
            },
            propType: { type: 'oneOf', value: ['large', 'default', 'small'] },
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        {
                            title: '',
                            value: 'large',
                        },
                        {
                            title: '',
                            value: 'default',
                        },
                        {
                            title: '',
                            value: 'small',
                        },
                    ],
                },
            },
            defaultValue: 'default',
        },
        {
            name: 'onChange ',
            title: { label: '', tip: 'onChange|' },
            propType: 'func',
        },
    ],
    configure: {
        supports: {
            style: true,
            events: [
                {
                    name: 'onChange',
                    template: "onChange(event,${extParams}){\n//\nconsole.log('onChange',event);}"
                }
            ]
        }
    },
    snippets: [
        {
            title: '',
            screenshot: avatarImg,
            schema: {
                componentName: 'ARadioGroup',
                props: {
                    "v-model:value": "A",
                    name: "radio-group",
                    options: [
                        {
                            label: 'A',
                            value: 'A',
                        },
                        {
                            label: 'B',
                            value: 'B',
                        },
                        {
                            label: 'C',
                            value: 'C',
                        },
                    ],
                }
            }
        }
    ]
};
