// @ts-ignore
import avatarImg from './__screenshots__/tag-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ATag',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ATag'
    },
    props: [
        {
            name: 'children',
            title: { label: '', tip: 'children|' },
            propType: 'string',
        },
        {
            name: 'closable',
            title: { label: '', tip: 'closable|' },
            propType: 'bool',
            defaultValue: false,
        },
        {
            name: 'color',
            title: { label: '', tip: 'color|' },
            propType: 'string',
        },
        {
            name: 'closeIcon',
            title: { label: '', tip: 'closeIcon|' },
            propType: 'node',
        },
        {
            name: 'visible',
            title: { label: '', tip: 'visible|' },
            propType: 'bool',
            defaultValue: true,
        },
        {
            name: 'icon',
            title: { label: '', tip: 'icon|' },
            propType: 'node',
        },
    ],
    configure: {
        supports: {
            style: true,
            events: [
                {
                    name: 'onClose',
                    template: "onClose(event,${extParams}){\n// \nconsole.log('onClose',event);}",
                },
            ],
        }
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "ATag",
                props: {
                    color: "magenta",
                    children: "",
                    visible: true
                }
            }
        }
    ]
};
