import { uuid } from '../../utils/index';
// @ts-ignore
import avatarImg from './__screenshots__/steps-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ASteps',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ASteps'
    },
    props: [
        {
            name: 'steps',
            title: { label: '', tip: 'steps|' },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'ObjectSetter',
                        props: {
                            config: {
                                items: [
                                    {
                                        name: 'key',
                                        title: 'key',
                                        setter: 'StringSetter',
                                        initialValue: (val) => val || uuid(),
                                    },
                                    {
                                        name: 'title',
                                        title: '',
                                        setter: 'StringSetter',
                                    },
                                    {
                                        name: 'subTitle',
                                        title: '',
                                        setter: 'StringSetter',
                                    },
                                    {
                                        name: 'description',
                                        title: '',
                                        setter: 'StringSetter',
                                    },
                                    {
                                        name: 'disabled',
                                        title: '',
                                        setter: 'BoolSetter',
                                        initialValue: false,
                                    },
                                    {
                                        name: 'status',
                                        title: { label: '', tip: '' },
                                        setter: {
                                            componentName: 'RadioGroupSetter',
                                            props: {
                                                options: [
                                                    {
                                                        title: 'wait',
                                                        value: 'wait',
                                                    },
                                                    {
                                                        title: 'process',
                                                        value: 'process',
                                                    },
                                                    {
                                                        title: 'finish',
                                                        value: 'finish',
                                                    },
                                                    {
                                                        title: 'error',
                                                        value: 'error',
                                                    },
                                                ],
                                            },
                                        },
                                        propType: {
                                            type: 'oneOf',
                                            value: ['wait', 'process', 'finish', 'error'],
                                        },
                                        defaultValue: 'wait',
                                    },
                                ],
                            },
                        },
                        initialValue: () => {
                            return {
                                key: `Steps${uuid()}`,
                                title: '',
                                disabled: false,
                            };
                        },
                    },
                },
            },
            extraProps: {
                getValue(target) {
                    const map = target.node.children.map((child) => {
                        const key = child.getPropValue('key') ? String(child.getPropValue('key')) : child.id;
                        return {
                            key,
                            title: child.getPropValue('title'),
                            subTitle: child.getPropValue('subTitle'),
                            description: child.getPropValue('description'),
                            disabled: child.getPropValue('disabled'),
                            status: child.getPropValue('status'),
                        };
                    });
                    return map;
                },
                setValue(target, value) {
                    const { node } = target;
                    const map = {};
                    if (!Array.isArray(value)) {
                        value = [];
                    }
                    value.forEach((item) => {
                        const tabItem = Object.assign({}, item);
                        // @ts-ignore
                        map[item.key] = tabItem;
                    });
                    node.children.mergeChildren((child) => {
                        const key = String(child.getPropValue('key'));
                        if (Object.hasOwnProperty.call(map, key)) {
                            // @ts-ignore
                            child.setPropValue('title', map[key].title);
                            // @ts-ignore
                            child.setPropValue('subTitle', map[key].subTitle);
                            // @ts-ignore
                            child.setPropValue('description', map[key].description);
                            // @ts-ignore
                            child.setPropValue('disabled', map[key].disabled);
                            // @ts-ignore
                            child.setPropValue('status', map[key].status);
                            // @ts-ignore
                            delete map[key];
                            return false;
                        }
                        return true;
                    }, () => {
                        const items = [];
                        for (const key in map) {
                            if (Object.hasOwnProperty.call(map, key)) {
                                items.push({
                                    componentName: 'AStepsStep',
                                    // @ts-ignore
                                    props: map[key],
                                });
                            }
                        }
                        return items;
                    }, (child1, child2) => {
                        const a = value.findIndex((item) => String(item.key) === String(child1.getPropValue('key')));
                        const b = value.findIndex((item) => String(item.key) === String(child2.getPropValue('key')));
                        return a - b;
                    });
                },
            },
        },
        {
            name: 'class',
            title: { label: '', tip: 'class|' },
            propType: 'string',
        },
        {
            name: 'type',
            title: {
                label: '',
                tip: 'type| `default`  `navigation` ',
            },
            propType: { type: 'oneOf', value: ['default', 'navigation'] },
            defaultValue: 'default',
        },
        {
            name: 'current',
            title: {
                label: '',
                tip: 'current| 0  Step  `status` ',
            },
            propType: 'number',
        },
        {
            name: 'direction',
            title: {
                label: '',
                tip: 'direction|`horizontal``vertical`',
            },
            propType: {
                type: 'oneOf',
                value: ['horizontal', 'vertical'],
            },
        },
        {
            name: 'labelPlacement',
            title: {
                label: '',
                tip: 'labelPlacement| `vertical` ',
            },
            propType: {
                type: 'oneOf',
                value: ['horizontal', 'vertical'],
            },
            defaultValue: 'horizontal',
        },
        {
            name: 'progressDot',
            title: {
                label: '',
                tip: 'progressDot| func',
            },
            propType: { type: 'oneOfType', value: ['bool', 'func'] },
        },
        {
            name: 'size',
            title: {
                label: '',
                tip: 'size|',
            },
            propType: {
                type: 'oneOf',
                value: ['default', 'small'],
            },
            defaultValue: 'default',
        },
        {
            name: 'status',
            title: {
                label: '',
                tip: 'status| `wait` `process` `finish` `error`',
            },
            propType: {
                type: 'oneOf',
                value: ['wait', 'process', 'finish', 'error'],
            },
            defaultValue: 'process',
        },
        {
            name: 'initial',
            title: {
                label: '',
                tip: 'initial| 0 ',
            },
            propType: 'number',
            defaultValue: 0,
        },
        {
            name: 'onChange ',
            title: { label: '', tip: 'onChange|' },
            propType: 'func',
        },
    ],
    configure: {
        component: {
            isContainer: true,
            nestingRule: { childWhitelist: ['AStepsStep'] },
        },
        supports: {
            style: true,
            events: [
                {
                    name: 'onChange',
                    template: "onChange(current,${extParams}){\n// \nconsole.log('onChange ',current);}",
                },
            ],
        }
    },
    snippets: [
        {
            title: '',
            screenshot: avatarImg,
            schema: {
                componentName: 'ASteps',
                props: {
                    current: 1,
                },
                children: [
                    {
                        componentName: 'AStepsStep',
                        props: {
                            title: 'Finished',
                            description: 'This is a description.',
                        },
                    },
                    {
                        componentName: 'AStepsStep',
                        props: {
                            title: 'In Progress',
                            subTitle: 'Left 00:00:08',
                            description: 'This is a description.',
                        },
                    },
                    {
                        componentName: 'AStepsStep',
                        props: {
                            title: 'Waiting',
                            description: 'This is a description.',
                        },
                    },
                ],
            },
        },
    ]
};
