// @ts-ignore
import avatarImg from './__screenshots__/switch-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ASwitch',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ASwitch',
    },
    configure: {
        props: [
            {
                name: 'defaultChecked',
                title: { label: '', tip: 'defaultChecked|' },
                propType: 'bool',
                defaultValue: false,
                setter: 'BoolSetter'
            },
            {
                name: 'checked',
                title: { label: '', tip: 'checked|' },
                propType: 'bool',
                defaultValue: false,
                setter: 'BoolSetter',
                supportVariable: true,
            },
            {
                name: 'autoFocus',
                title: { label: '', tip: 'autoFocus|' },
                propType: 'bool',
                defaultValue: false,
                setter: 'BoolSetter'
            },
            {
                name: 'checkedChildren',
                title: { label: '', tip: 'checkedChildren|' },
                propType: 'string',
                setter: 'StringSetter'
            },
            {
                name: 'unCheckedChildren',
                title: { label: '', tip: 'unCheckedChildren|' },
                propType: 'string',
                setter: 'StringSetter'
            },
            {
                name: 'disabled',
                title: { label: '', tip: 'disabled|' },
                propType: 'bool',
                defaultValue: false,
                setter: 'BoolSetter'
            },
            {
                name: 'loading',
                title: { label: '', tip: 'loading|' },
                propType: 'bool',
                defaultValue: false,
                setter: 'BoolSetter'
            },
            {
                name: 'size',
                title: { label: '', tip: 'size|' },
                propType: { type: 'oneOf', value: ['default', 'small'] },
                setter: {
                    componentName: 'RadioGroupSetter',
                    props: {
                        options: [
                            {
                                title: '',
                                value: 'default',
                            },
                            {
                                title: '',
                                value: 'small',
                            },
                        ],
                    },
                },
                defaultValue: 'default',
            },
        ],
        supports: {
            style: true,
            events: [
                {
                    name: 'onChange',
                    template: "onChange(checked,event,${extParams}){\n// \nconsole.log('onChange',checked,event);}",
                },
                {
                    name: 'onClick',
                    template: "onClick(checked,event,${extParams}){\n// \nconsole.log('onClick',checked,event);}",
                },
            ]
        }
    },
    snippets: [
        {
            title: '',
            screenshot: avatarImg,
            schema: {
                componentName: "ASwitch",
                props: {
                    defaultChecked: true
                }
            }
        }
    ]
};
