import { uuid } from '../../utils/index';
export default {
    group: 'ant-vue',
    componentName: 'ACollapse',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ACollapse'
    },
    props: [
        {
            name: 'bordered',
            title: { label: '', tip: 'bordered|' },
            propType: 'bool',
            defaultValue: true,
            setter: 'BoolSetter',
            supportVariable: true
        },
        {
            name: 'accordion',
            title: { label: '', tip: 'accordion|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter',
            supportVariable: true
        },
        {
            name: 'collapsible',
            title: { label: '', tip: 'collapsible|' },
            propType: {
                type: 'oneOf',
                value: ['-', 'header', 'disabled'],
            },
        },
        {
            name: 'expandIconPosition',
            title: { label: '', tip: 'expandIconPosition|' },
            propType: { type: 'oneOf', value: ['left', 'right'] },
        },
        {
            name: 'destroyInactivePanel',
            title: { label: '', tip: 'destroyInactivePanel|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter',
            supportVariable: true
        },
        {
            name: 'ghost',
            title: { label: '', tip: 'ghost|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter',
            supportVariable: true
        },
        {
            name: 'collapses',
            title: { label: '', tip: 'collapses' },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'ObjectSetter',
                        props: {
                            config: {
                                items: [
                                    {
                                        name: 'key',
                                        title: 'key',
                                        setter: 'StringSetter',
                                        initialValue: (val) => val || uuid()
                                    },
                                    {
                                        name: 'header',
                                        title: '',
                                        setter: 'StringSetter',
                                        initialValue: ''
                                    },
                                ],
                            },
                        },
                        initialValue: () => {
                            return {
                                key: uuid(),
                                header: '',
                                showArrow: true,
                                collapsible: undefined,
                                forceRender: false,
                            };
                        },
                    },
                },
            },
            extraProps: {
                getValue(target) {
                    console.log('getValue', target.node.children.length);
                    const map = target.node.children.map((child) => {
                        const key = child.getPropValue('key') ? String(child.getPropValue('key')) : child.id;
                        return {
                            key,
                            header: child.getPropValue('header'),
                            showArrow: child.getPropValue('showArrow'),
                            collapsible: child.getPropValue('collapsible'),
                            forceRender: child.getPropValue('forceRender'),
                        };
                    });
                    return map;
                },
                setValue(target, value) {
                    const { node } = target;
                    const map = {};
                    // console.log('setValue',value);
                    if (!Array.isArray(value)) {
                        value = [];
                    }
                    value.forEach((item) => {
                        const tabItem = Object.assign({}, item);
                        // @ts-ignore
                        map[item.key] = tabItem;
                    });
                    node.children.mergeChildren((child) => {
                        const key = String(child.getPropValue('key'));
                        if (Object.hasOwnProperty.call(map, key)) {
                            // @ts-ignore
                            child.setPropValue('header', map[key].header);
                            // @ts-ignore
                            child.setPropValue('showArrow', map[key].showArrow);
                            // @ts-ignore
                            child.setPropValue('collapsible', map[key].collapsible);
                            // @ts-ignore
                            child.setPropValue('forceRender', map[key].forceRender);
                            // @ts-ignore
                            delete map[key];
                            return false;
                        }
                        return true;
                    }, () => {
                        const items = [];
                        for (const key in map) {
                            if (Object.hasOwnProperty.call(map, key)) {
                                items.push({
                                    componentName: 'ACollapsePanel',
                                    // @ts-ignore
                                    props: map[key],
                                });
                            }
                        }
                        return items;
                    }, (child1, child2) => {
                        const a = value.findIndex((item) => String(item.key) === String(child1.getPropValue('key')));
                        const b = value.findIndex((item) => String(item.key) === String(child2.getPropValue('key')));
                        return a - b;
                    });
                },
            },
        },
        {
            name: 'defaultActiveKey',
            title: { label: ' key', tip: 'defaultActiveKey| key' },
            propType: {
                type: 'oneOfType',
                value: [
                    'string',
                    { type: 'arrayOf', value: 'string' },
                    'number',
                    { type: 'arrayOf', value: 'number' },
                ],
            },
        },
        {
            name: 'activeKey',
            title: {
                label: ' tab  key',
                tip: 'activeKey| tab  key',
            },
            propType: {
                type: 'oneOfType',
                value: [
                    'string',
                    { type: 'arrayOf', value: 'string' },
                    'number',
                    { type: 'arrayOf', value: 'number' },
                ],
            },
        }
    ],
    configure: {
        component: {
            isContainer: true,
        },
        supports: {
            style: true
        },
        events: [
            {
                name: 'onChange ',
                template: "onChange (key,${extParams}){\n// \nconsole.log('onChange ',key);}",
            }
        ]
    },
    snippets: [
        {
            title: "",
            screenshot: "https://alifd.alicdn.com/fusion-cool/icons/icon-antd/collapse-1.png",
            schema: {
                componentName: "ACollapse",
                props: {
                    defaultActiveKey: ['collapse-item-1']
                },
                children: [
                    {
                        componentName: 'ACollapsePanel',
                        props: {
                            header: '1',
                            key: 'collapse-item-1',
                        },
                    },
                    {
                        componentName: 'ACollapsePanel',
                        props: {
                            header: '2',
                            key: 'collapse-item-2',
                        }
                    }
                ]
            }
        }
    ]
};
