/* eslint-disable */
// @ts-ignore
import avatarImg from './__screenshots__/slider-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ASlider',
    title: '()',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ASlider',
    },
    props: [
        {
            name: 'defaultValue',
            title: {
                label: '',
                tip: 'defaultValue| `range`  false  number \\[number, number]',
            },
            propType: {
                type: 'oneOfType',
                value: ['number', { type: 'arrayOf', value: 'number' }],
            },
        },
        {
            name: 'range',
            title: { label: '', tip: 'range|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter',
            setValue(target, range) {
                let defaultValue = target.node.getPropValue('defaultValue');
                if (range) {
                    defaultValue = Array.isArray(defaultValue) ? defaultValue : [0, defaultValue];
                }
                else {
                    defaultValue = Array.isArray(defaultValue)
                        ? defaultValue[1] || defaultValue[0]
                        : defaultValue;
                }
                target.node.setPropValue('defaultValue', defaultValue);
            },
        },
        {
            name: 'value',
            title: {
                label: '',
                tip: 'value| `range`  false  number \\[number, number]',
            },
            propType: {
                type: 'oneOfType',
                value: ['number', { type: 'arrayOf', value: 'number' }],
            },
        },
        {
            name: 'allowClear',
            title: { label: '', tip: 'allowClear|' },
            condition(target) {
                return target.getProps().getPropValue('range');
            },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'disabled',
            title: {
                label: '',
                tip: 'disabled|',
            },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'dots',
            title: { label: '', tip: 'dots|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'max',
            title: { label: '', tip: 'max|' },
            propType: 'number',
            setter: 'NumberSetter'
        },
        {
            name: 'min',
            title: { label: '', tip: 'min|' },
            propType: 'number',
            setter: 'NumberSetter'
        },
        {
            name: 'reverse',
            title: { label: '', tip: 'reverse|' },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
        {
            name: 'step',
            title: {
                label: '',
                tip: 'step| 0 (max - min)  `marks`  `step`  null Slider  marks ',
            },
            propType: 'number',
            setter: 'NumberSetter'
        },
        {
            name: 'vertical',
            title: {
                label: '',
                tip: 'vertical| true Slider ',
            },
            propType: 'bool',
            defaultValue: false,
            setter: 'BoolSetter'
        },
    ],
    configure: {
        component: {
            isContainer: true
        },
        supports: {
            style: true,
            events: [
                {
                    name: 'onAfterChange',
                    template: "onAfterChange(value,${extParams}){\n//  onmouseup \nconsole.log('onAfterChange',value);}",
                },
                {
                    name: 'onChange',
                    template: "onChange(value,${extParams}){\n//  Slider \nconsole.log('onChange',value);}",
                },
            ]
        }
    },
    snippets: [
        {
            title: "()",
            screenshot: avatarImg,
            schema: {
                componentName: "ASlider",
                props: {
                    defaultValue: 30
                }
            }
        }
    ]
};
