import { uuid } from '../../utils/index';
// @ts-ignore
import avatarImg from './__screenshots__/transfer-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ATransfer',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ATransfer'
    },
    props: [
        {
            name: 'dataSource',
            title: {
                label: '',
                tip: 'dataSource|`targetKeys` ',
            },
            propType: { type: 'arrayOf', value: 'object' },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'ObjectSetter',
                        initialValue: () => {
                            return {
                                key: uuid(),
                            };
                        },
                        props: {
                            config: {
                                items: [
                                    {
                                        name: 'key',
                                        title: 'key',
                                        setter: 'StringSetter',
                                        initialValue: (val) => val || uuid(),
                                    },
                                    {
                                        name: 'title',
                                        title: 'title',
                                        setter: 'StringSetter',
                                        isRequired: true,
                                    },
                                    {
                                        name: 'description',
                                        title: '',
                                        setter: 'StringSetter',
                                    },
                                    {
                                        name: 'disabled',
                                        title: '',
                                        setter: ['BoolSetter', 'FunctionSetter'],
                                    },
                                ],
                            },
                        },
                    },
                },
            },
        },
        {
            name: 'v-model:selectedKeys',
            title: { label: '', tip: 'v-model:selectedKeys|' },
            propType: { type: 'arrayOf', value: 'string' },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: 'StringSetter'
                }
            },
            supportVariable: true,
        },
        {
            name: 'v-model:targetKeys',
            title: {
                label: '',
                tip: 'v-model:targetKeys| key ',
            },
            propType: { type: 'arrayOf', value: 'string' },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: 'StringSetter'
                }
            },
            supportVariable: true,
        },
        {
            title: '',
            display: 'block',
            type: 'group',
            items: [
                {
                    name: 'oneWay',
                    title: { label: '', tip: 'oneWay|' },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'disabled',
                    title: { label: '', tip: 'disabled|' },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'showSearch',
                    title: { label: '', tip: 'showSearch|' },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: false,
                },
                {
                    name: 'showSelectAll',
                    title: { label: '', tip: 'showSelectAll|' },
                    propType: 'bool',
                    setter: 'BoolSetter',
                    defaultValue: true,
                },
                {
                    name: 'pagination',
                    title: {
                        label: '',
                        tip: 'pagination|',
                    },
                    setter: [
                        'BoolSetter',
                        {
                            componentName: 'ObjectSetter',
                            props: {
                                config: {
                                    items: [
                                        {
                                            name: 'pageSize',
                                            title: '',
                                            setter: 'NumberSetter',
                                        },
                                        {
                                            name: 'simple',
                                            title: '',
                                            setter: 'BoolSetter',
                                        },
                                        {
                                            name: 'showSizeChanger',
                                            title: '',
                                            setter: 'BoolSetter',
                                        },
                                    ],
                                },
                            },
                        }
                    ],
                    propType: {
                        type: 'oneOfType',
                        value: ['bool', 'object']
                    },
                    defaultValue: false,
                },
                {
                    name: 'render',
                    title: {
                        label: '',
                        tip: 'render| dataSource  element label  elementvalue  title',
                    },
                    propType: 'func',
                    setter: [
                        {
                            componentName: 'FunctionSetter',
                            props: {
                                template: 'renderItem(record,${extParams}){\n// \nreturn record.title;\n}',
                            },
                        },
                        'VariableSetter',
                    ],
                },
                {
                    name: 'filterOption',
                    title: {
                        label: '',
                        tip: 'filterOption| `inputValue` `option`  `option`  true false',
                    },
                    propType: 'func',
                    setter: [
                        {
                            componentName: 'FunctionSetter',
                            props: {
                                template: 'filterOption(inputValue,option,${extParams}){\n//  inputValue option  option  true false\n\n}',
                            },
                        },
                        'VariableSetter',
                    ],
                },
                {
                    name: 'footer',
                    title: {
                        label: '',
                        tip: 'footer | ',
                    },
                    propType: 'func',
                    setter: [
                        {
                            componentName: 'FunctionSetter',
                            props: {
                                template: 'renderItem(item,${extParams}){\n// \nreturn `item`;\n}',
                            },
                        },
                        {
                            componentName: 'SlotSetter',
                            title: '',
                            initialValue: {
                                type: 'JSSlot',
                                value: [],
                                params: ['props', 'info']
                            },
                        },
                        'VariableSetter',
                    ],
                },
                {
                    name: 'operations',
                    title: {
                        label: '',
                        tip: 'operations|',
                    },
                    propType: { type: 'arrayOf', value: 'string' },
                },
                {
                    name: 'titles',
                    title: { label: '', tip: 'titles|' },
                    propType: { type: 'arrayOf', value: 'node' },
                },
                {
                    name: 'selectAllLabels',
                    title: {
                        label: '',
                        tip: 'selectAllLabels|',
                    },
                    propType: {
                        type: 'arrayOf',
                        value: { type: 'oneOfType', value: ['node', 'func'] },
                    },
                }
            ]
        }
    ],
    configure: {
        supports: {
            style: true,
            events: [
                {
                    name: 'onChange',
                    template: "onChange(targetKeys,direction,moveKeys,${extParams}){\n// \nconsole.log('onChange',targetKeys,direction,moveKeys);}",
                },
                {
                    name: 'onScroll',
                    template: "onScroll(direction,event,${extParams}){\n// \nconsole.log('onScroll',direction,event);}",
                },
                {
                    name: 'onSearch',
                    template: "onSearch(direction,value,${extParams}){\n// \nconsole.log('onSearch',direction,value);}",
                },
                {
                    name: 'onSelectChange',
                    template: "onSelectChange(sourceSelectedKeys,targetSelectedKeys,${extParams}){\n// \nconsole.log('onSelectChange',sourceSelectedKeys,targetSelectedKeys);}",
                },
            ]
        }
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "ATransfer",
                props: {
                    dataSource: [
                        {
                            key: uuid(),
                            title: '1'
                        },
                        {
                            key: uuid(),
                            title: 'lyllovelemon'
                        },
                        {
                            key: uuid(),
                            title: 'lyltest'
                        },
                        {
                            key: uuid(),
                            title: 'content 4'
                        },
                        {
                            key: uuid(),
                            title: 'content 5'
                        }
                    ],
                    render: {
                        type: "JSFunction",
                        value: `function renderItem(record, extParams) {
              return record.title;
            }`,
                    }
                }
            }
        }
    ]
};
