// @ts-ignore
import Image from './__screenshots__/image-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'AImage',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'AImage'
    },
    props: [
        {
            name: 'src',
            title: { label: '', tip: 'src|' },
            propType: { type: 'string', isRequired: true },
        },
        {
            name: 'alt',
            title: { label: '', tip: 'alt|' },
            propType: 'string',
        },
        {
            name: 'preview',
            title: { label: '', tip: 'preview|' },
            defaultValue: true,
            propType: 'bool',
        },
        {
            name: 'fallback',
            title: { label: '', tip: 'fallback|' },
            propType: 'string',
        },
        {
            name: 'width',
            title: { label: '', tip: '' },
            propType: { type: 'oneOfType', value: ['number', 'string'] },
        },
        {
            name: 'height',
            title: { label: '', tip: 'height|' },
            propType: { type: 'oneOfType', value: ['number', 'string'] },
        },
        {
            name: 'previewMask',
            title: { label: 'mask', tip: 'previewMask| mask' },
            propType: { type: 'oneOfType', value: ['bool', 'func', 'node'] },
            setter: [
                'BoolSetter',
                {
                    componentName: 'SlotSetter',
                    initialValue: {
                        type: 'JSSlot',
                        value: []
                    }
                },
                {
                    componentName: 'FunctionSetter',
                    props: {
                        template: 'showPreviewMask(${extParams}){\n// ;\n}',
                    },
                },
            ],
            defaultValue: false
        }
    ],
    configure: {
        supports: {
            style: true,
            events: [
                {
                    name: 'onError',
                    template: "onError(event,${extParams}){\n// \nconsole.log('onError',e);}",
                },
            ],
        }
    },
    snippets: [
        {
            title: "",
            screenshot: Image,
            schema: {
                componentName: "AImage",
                props: {
                    src: Image,
                    width: 120,
                    height: 120
                }
            }
        }
    ]
};
