import { uuid } from '../../utils/index';
// @ts-ignore
import avatarImg from './__screenshots__/timeline-1.jpg?inline';
export default {
    group: 'ant-vue',
    componentName: 'ATimeline',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ATimeline'
    },
    props: [
        {
            name: 'steps',
            title: { label: '', tip: 'steps|' },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'ObjectSetter',
                        props: {
                            config: {
                                items: [
                                    {
                                        name: 'key',
                                        title: 'key',
                                        setter: 'StringSetter',
                                        initialValue: (val) => val || uuid(),
                                    },
                                    {
                                        name: 'color',
                                        title: '',
                                        setter: 'StringSetter',
                                    },
                                    {
                                        name: 'dot',
                                        title: '',
                                        setter: 'node',
                                    },
                                    {
                                        name: 'label',
                                        title: '',
                                        setter: 'StringSetter',
                                    },
                                    {
                                        name: 'position',
                                        title: {
                                            label: '',
                                            tip: '',
                                        },
                                        propType: {
                                            type: 'oneOf',
                                            value: ['left', 'right'],
                                        },
                                        setter: [
                                            {
                                                componentName: 'RadioGroupSetter',
                                                props: {
                                                    options: [
                                                        {
                                                            title: 'left',
                                                            value: 'left',
                                                        },
                                                        {
                                                            title: 'right',
                                                            value: 'right',
                                                        },
                                                    ],
                                                },
                                            },
                                            'VariableSetter',
                                        ],
                                    },
                                ],
                            },
                        },
                        initialValue: () => {
                            return {
                                key: `timeLine${uuid()}`,
                                label: '',
                            };
                        },
                    },
                },
            },
            extraProps: {
                getValue(target, fieldValue) {
                    const map = target.node.children.map((child) => {
                        const key = child.getPropValue('key') ? String(child.getPropValue('key')) : child.id;
                        const result = { key };
                        ['color', 'dot', 'label', 'position'].forEach((propKey) => {
                            // @ts-ignore
                            result[propKey] = child.getPropValue(propKey);
                        });
                        return result;
                    });
                    return map.length === 0 ? fieldValue : map;
                },
                setValue(target, value) {
                    const { node } = target;
                    const map = {};
                    if (!Array.isArray(value)) {
                        value = [];
                    }
                    value.forEach((item) => {
                        const tabItem = Object.assign({}, item);
                        // @ts-ignore
                        map[item.key] = tabItem;
                    });
                    node.children.mergeChildren((child) => {
                        const key = String(child.getPropValue('key'));
                        if (Object.hasOwnProperty.call(map, key)) {
                            ['color', 'dot', 'label', 'position'].forEach((propKey) => {
                                // @ts-ignore
                                child.setPropValue(propKey, map[key][propKey]);
                            });
                            // @ts-ignore
                            delete map[key];
                            return false;
                        }
                        return true;
                    }, () => {
                        const items = [];
                        for (const key in map) {
                            if (Object.hasOwnProperty.call(map, key)) {
                                items.push({
                                    componentName: 'ATimelineItem',
                                    // @ts-ignore
                                    props: map[key],
                                });
                            }
                        }
                        return items;
                    }, (child1, child2) => {
                        const a = value.findIndex((item) => String(item.key) === String(child1.getPropValue('key')));
                        const b = value.findIndex((item) => String(item.key) === String(child2.getPropValue('key')));
                        return a - b;
                    });
                },
            },
        },
        {
            name: 'mode',
            title: {
                label: '',
                tip: 'mode| `mode` ',
            },
            propType: { type: 'oneOf', value: ['left', 'alternate', 'right'] },
        },
        {
            name: 'pending',
            title: {
                label: '',
                tip: 'pending|',
            },
            propType: 'bool',
        },
        {
            name: 'pendingDot',
            title: {
                label: '',
                tip: 'pendingDot|',
            },
            propType: { type: 'oneOfType', value: ['string', 'node'] },
        },
        {
            name: 'reverse',
            title: { label: '', tip: 'reverse|' },
            propType: 'bool',
            defaultValue: false,
        }
    ],
    configure: {
        supports: {
            style: true
        }
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "ATimeline",
                props: {
                    steps: [
                        {
                            key: 'timeLinei5wd',
                            label: '1',
                        },
                        {
                            key: 'timeLinei5wx',
                            label: '2',
                        }
                    ],
                }
            }
        }
    ]
};
