import { uuid } from '../../utils/index';
// @ts-ignore
import avatarImg from './__screenshots__/descriptions-1.jpg?inline';
export default {
    group: 'ant-vue',
    componentName: 'ADescriptions',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ADescriptions'
    },
    props: [
        {
            name: 'title',
            title: {
                label: '',
                tip: 'title|',
            },
            propType: {
                type: 'oneOfType',
                value: ['string', 'node'],
            },
        },
        {
            name: 'items',
            title: {
                label: '',
                tip: 'items|',
            },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'ObjectSetter',
                        props: {
                            config: {
                                items: [
                                    {
                                        name: 'key',
                                        title: 'key',
                                        setter: 'StringSetter',
                                        initialValue: (val) => val || uuid(),
                                        condition: () => false,
                                    },
                                    {
                                        name: 'label',
                                        title: '',
                                        setter: 'StringSetter',
                                        initialValue: '',
                                    },
                                    {
                                        name: 'span',
                                        title: '',
                                        setter: 'NumberSetter',
                                        initialValue: 1,
                                    },
                                    {
                                        name: 'children',
                                        title: '',
                                        setter: {
                                            componentName: 'SlotSetter',
                                            initialValue: {
                                                type: 'JSSlot',
                                                value: [],
                                            },
                                        },
                                    },
                                ],
                            },
                        },
                        initialValue: () => {
                            return {
                                key: uuid(),
                                label: '',
                                span: 1,
                                children: {
                                    type: 'JSSlot',
                                    value: [],
                                },
                            };
                        },
                    },
                },
            },
            extraProps: {
                getValue(target) {
                    const map = target.node.children.map((child) => {
                        const key = child.getPropValue('key') ? String(child.getPropValue('key')) : child.id;
                        return {
                            key,
                            label: child.getPropValue('label'),
                            span: child.getPropValue('span'),
                            children: child.getPropValue('children'),
                        };
                    });
                    return map;
                },
                setValue(target, value) {
                    const { node } = target;
                    const map = {};
                    if (!Array.isArray(value)) {
                        value = [];
                    }
                    value.forEach((item) => {
                        const tabItem = Object.assign({}, item);
                        // @ts-ignore
                        map[item.key] = tabItem;
                    });
                    node.children.mergeChildren((child) => {
                        const key = String(child.getPropValue('key'));
                        if (Object.hasOwnProperty.call(map, key)) {
                            // @ts-ignore
                            child.setPropValue('label', map[key].label);
                            // @ts-ignore
                            child.setPropValue('span', map[key].span);
                            // @ts-ignore
                            child.setPropValue('children', map[key].children);
                            // @ts-ignore
                            delete map[key];
                            return false;
                        }
                        return true;
                    }, () => {
                        const items = [];
                        for (const key in map) {
                            if (Object.hasOwnProperty.call(map, key)) {
                                items.push({
                                    componentName: 'Descriptions.Item',
                                    // @ts-ignore
                                    props: map[key],
                                });
                            }
                        }
                        return items;
                    }, (child1, child2) => {
                        const a = value.findIndex((item) => String(item.key) === String(child1.getPropValue('key')));
                        const b = value.findIndex((item) => String(item.key) === String(child2.getPropValue('key')));
                        return a - b;
                    });
                }
            },
        },
        {
            name: 'bordered',
            title: { label: '', tip: 'bordered|' },
            propType: 'bool',
            defaultValue: false,
        },
        {
            name: 'column',
            title: {
                label: '',
                tip: 'column|',
            },
            propType: 'number',
            defaultValue: 3,
        },
        {
            name: 'size',
            title: {
                label: '',
                tip: 'size| `middle` `small`,  `bordered={true}` ',
            },
            propType: { type: 'oneOf', value: ['default', 'middle', 'small'] },
            defaultValue: 'middle',
        },
        {
            name: 'layout',
            title: { label: '', tip: 'layout|' },
            propType: { type: 'oneOf', value: ['horizontal', 'vertical'] },
            defaultValue: 'horizontal',
        },
        {
            name: 'colon',
            title: {
                label: '',
                tip: 'colon| `Descriptions.Item`  `colon` ',
            },
            propType: 'bool',
            defaultValue: true,
        }
    ],
    configure: {
        supports: {
            style: true
        }
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "ADescriptions",
                props: {
                    title: "",
                    items: [
                        {
                            label: "",
                            children: "li yi li"
                        }
                    ]
                }
            }
        }
    ]
};
