import { uuid } from '../../utils/index';
// @ts-ignore
import avatarImg from './__screenshots__/carousel-1.jpg?inline';
export default {
    group: 'ant-vue',
    componentName: 'ACarousel',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ACarousel'
    },
    props: [
        {
            name: 'autoplay',
            title: { label: '', tip: 'autoplay|' },
            propType: 'bool',
            setter: 'BoolSetter',
            defaultValue: false
        },
        {
            name: 'dotPosition',
            title: { label: '', tip: 'dotPosition|' },
            propType: { type: 'oneOf', value: ['top', 'bottom', 'left', 'right'] },
            setter: {
                componentName: 'SelectSetter',
                props: {
                    options: [
                        { title: 'top', value: 'top' },
                        { title: 'bottom', value: 'bottom' },
                        { title: 'left', value: 'left' },
                        { title: 'right', value: 'right' }
                    ]
                }
            },
            defaultValue: 'bottom'
        },
        {
            name: 'dot',
            title: { label: '', tip: 'dot|' },
            propType: 'bool',
            setter: 'BoolSetter',
            defaultValue: true
        },
        {
            name: 'dotsClass',
            title: { label: '', tip: 'dotsClass|' },
            propType: 'string',
            setter: 'StringSetter',
            defaultValue: 'slick-dots'
        },
        {
            name: 'easing',
            title: { label: '', tip: 'easing|' },
            propType: 'string',
            defaultValue: 'linear'
        },
        {
            name: 'effect',
            title: { label: '', tip: 'effect|' },
            propType: { type: 'oneOf', value: ['scrollx', 'fade'] },
        },
        {
            name: 'items',
            title: { label: '', tip: 'items|' },
            setter: {
                componentName: 'ArraySetter',
                props: {
                    itemSetter: {
                        componentName: 'ObjectSetter',
                        initialValue: () => {
                            return {
                                key: uuid(),
                            };
                        },
                    },
                },
            },
            extraProps: {
                getValue(target) {
                    console.log('getValue', target.node.children.length);
                    const map = target.node.children.map((child) => {
                        const key = child.getPropValue('key') ? String(child.getPropValue('key')) : child.id;
                        return { key };
                    });
                    return map;
                },
                setValue(target, value) {
                    const { node } = target;
                    const map = {};
                    if (!Array.isArray(value)) {
                        value = [];
                    }
                    value.forEach((item) => {
                        const tabItem = Object.assign({}, item);
                        // @ts-ignore
                        map[item.key] = tabItem;
                    });
                    node.children.mergeChildren((child) => {
                        const key = String(child.getPropValue('key'));
                        if (Object.hasOwnProperty.call(map, key)) {
                            // @ts-ignore
                            delete map[key];
                            return false;
                        }
                        return true;
                    }, () => {
                        const items = [];
                        for (const key in map) {
                            if (Object.hasOwnProperty.call(map, key)) {
                                items.push({
                                    componentName: 'Card',
                                    // @ts-ignore
                                    props: map[key],
                                });
                            }
                        }
                        return items;
                    }, (child1, child2) => {
                        const a = value.findIndex((item) => String(item.key) === String(child1.getPropValue('key')));
                        const b = value.findIndex((item) => String(item.key) === String(child2.getPropValue('key')));
                        return a - b;
                    });
                },
            },
        },
        {
            name: 'afterChange',
            title: { label: '', tip: 'afterChange|' },
            propType: 'func',
        },
        {
            name: 'beforeChange',
            title: { label: '', tip: 'beforeChange|' },
            propType: 'func',
        },
    ],
    configure: {
        component: {
            isContainer: true,
        },
        supports: {
            style: true
        },
        events: [
            {
                name: 'onChange ',
                template: "onChange (time,timeString,${extParams}){\n// \nconsole.log('onChange ',time,timeString);}",
            },
            {
                name: 'onOpenChange',
                template: "onOpenChange(open,${extParams}){\n// /\nconsole.log('onOpenChange',open);}",
            }
        ]
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "ACarousel",
                children: [
                    {
                        componentName: "ACard",
                        props: {
                            key: "panel 1"
                        }
                    },
                    {
                        componentName: "ACard",
                        props: {
                            key: "panel 2"
                        }
                    },
                    {
                        componentName: "ACard",
                        props: {
                            key: "panel 3"
                        }
                    }
                ]
            }
        }
    ]
};
