// @ts-ignore
import avatarImg from './__screenshots__/dropdown-1.png?inline';
export default {
    group: 'ant-vue',
    componentName: 'ADropdown',
    title: '',
    category: '',
    npm: {
        destructuring: true,
        componentName: 'ADropdown'
    },
    props: [
        {
            name: 'visible',
            title: { label: '', tip: 'visible|' },
            propType: { type: 'oneOf', value: [true, false, '-'] },
            defaultValue: '-',
            setter: {
                componentName: 'RadioGroupSetter',
                props: {
                    options: [
                        {
                            title: '',
                            value: '-',
                        },
                        {
                            title: '',
                            value: true,
                        },
                        {
                            title: '',
                            value: false,
                        },
                    ],
                },
            },
            extraProps: {
                getValue(target) {
                    const { node } = target;
                    let value = node.getPropValue('visible');
                    if (value === undefined) {
                        value = '-';
                    }
                    return value;
                },
                setValue(target, value) {
                    const { node } = target;
                    if (value === '-') {
                        setTimeout(() => {
                            node.clearPropValue('visible');
                        });
                    }
                },
            },
        },
        {
            name: 'arrow',
            title: { label: '', tip: 'arrow|' },
            propType: 'bool',
            defaultValue: false,
        },
        {
            name: 'disabled',
            title: { label: '', tip: 'disabled|' },
            propType: 'bool',
        },
        {
            name: 'overlay',
            title: { label: '', tip: 'overlay|' },
            propType: { type: 'oneOfType', value: ['node', 'func'] },
        },
        {
            name: 'size',
            title: { label: '', tip: 'size|' },
            propType: { type: 'oneOf', value: ['default', 'small', 'large'] },
            setter: {
                componentName: "RadioGroupSetter",
                props: {
                    options: [
                        { label: 'default', value: 'default' },
                        { label: 'large', value: 'large' },
                        { label: 'small', value: 'small' },
                    ],
                }
            },
            defaultValue: 'default'
        },
        {
            name: 'placement',
            title: {
                label: '',
                tip: 'placement|`bottomLeft` `bottomCenter` `bottomRight` `topLeft` `topCenter` `topRight`',
            },
            propType: {
                type: 'oneOf',
                value: ['bottomLeft', 'bottomCenter', 'bottomRight', 'topLeft', 'topCenter', 'topRight'],
            },
        },
        {
            name: 'trigger',
            title: {
                label: '',
                tip: 'trigger|,  hover',
            },
            propType: {
                type: 'arrayOf',
                value: { type: 'oneOf', value: ['click', 'hover', 'contextMenu'] },
            },
        },
        {
            name: 'onVisibleChange',
            title: {
                label: '',
                tip: 'onVisibleChange| `visible`',
            },
            propType: 'func',
        },
    ],
    configure: {
        component: { isContainer: true },
        supports: {
            style: true,
            events: [
                {
                    name: 'visibleChange',
                    template: "visibleChange(open,${extParams}){\n// \nconsole.log('visibleChange',open);}",
                },
            ]
        }
    },
    snippets: [
        {
            title: "",
            screenshot: avatarImg,
            schema: {
                componentName: "ADropdown",
                props: {
                    overlay: {
                        type: 'JSSlot',
                        value: [
                            {
                                componentName: 'AMenu',
                                props: {
                                    items: [
                                        {
                                            key: 'timeLinei5wd',
                                            category: 'Item',
                                            title: '',
                                        },
                                    ],
                                },
                                children: [
                                    {
                                        componentName: 'AMenuItem',
                                        id: 'node_ocky01yzdq3',
                                        props: {
                                            key: 'timeLinei5we',
                                            category: 'Item',
                                            title: '',
                                            children: '',
                                        },
                                    },
                                ],
                            },
                        ],
                    },
                },
                children: [
                    {
                        componentName: 'AButton',
                        props: {
                            type: 'link',
                            children: {
                                type: 'JSSlot',
                                value: [
                                    {
                                        componentName: 'ATypographyText',
                                        props: {
                                            children: 'Hover me',
                                            style: {
                                                color: 'inherit',
                                            },
                                        },
                                    }
                                ]
                            }
                        }
                    }
                ]
            }
        }
    ]
};
